export default {
  "article-body-example.hbs": "    <NyprOArticleBody @body='<p><strong>Hello</strong> <em>world</em></p>' />",
  "article-footer-example.hbs": "    <NyprOArticleFooter as |footer|>\n      <footer.tags @tags={{array 'bronx' 'police'}} as |Tag tag|>\n        <Tag @url='http://example.com/tags/{{tag}}'/>\n      </footer.tags>\n\n      <footer.contact>\n        <p><strong>Do you know the scoop?</strong> &nbsp; <a href=\"\">Comment</a> below or <a href=\"\">Send us a Tip</a></p>\n      </footer.contact>\n\n      <footer.donate\n        @linkText='Donate'\n        @linkUrl='https://pledge3.wnyc.org/donate/gothamist/onestep'>\n        <p>\n          Hey, do you know that it  takes money to write these stories? Help us tell more with your help today.\n        </p>\n      </footer.donate>\n\n      <footer.comments>\n        comments go here\n      </footer.comments>\n    </NyprOArticleFooter>",
  "article-header-example.hbs": "    <NyprOArticleHeader as |header|>\n      <header.top as |top|>\n        <top.breadcrumb @breadcrumbs={{STORY.breadcrumbs}}/>\n        <top.headline>\n          {{STORY.title}}\n        </top.headline>\n\n        <top.meta @galleryPhotosCount={{STORY.gallery.length}} as |meta|>\n          <meta.byline @authors={{STORY.authors}} @sponsor={{STORY.sponsor}}/>\n          <meta.publishDate @publishDate={{STORY.publishDate}}/>\n        </top.meta>\n      </header.top>\n\n      <header.lead as |lead|>\n        <lead.left>\n          <NyprMShareTools @vertical={{true}} as |share|>\n            <share.link @service='twitter'/>\n            <share.link @service='facebook'/>\n            <share.link @service='instagram'/>\n            <share.link @service='email'/>\n          </NyprMShareTools>\n        </lead.left>\n\n        <lead.center>\n          {{#if STORY.gallery}}\n            <NyprMGalleryLead @slides={{STORY.gallery}} />\n          {{else}}\n            <NyprMFigure\n              @srcS={{STORY.leadImage.src}}\n              @caption={{STORY.leadImage.caption}}\n              @credit={{STORY.leadImage.credit}}\n            />\n          {{/if}}\n        </lead.center>\n\n        <lead.right>\n          <NyprORelatedContent @items={{STORY.relatedItems}}/>\n        </lead.right>\n\n      </header.lead>\n    </NyprOArticleHeader>",
  "block": "    <p>\n      <strong>You searched for:</strong> <code>{{blockQuery}}</code>\n    </p>\n\n    <!-- BEGIN-SNIPPET search-block-usage.hbs -->\n    <NyprMInlineSearch @formClass=\"c-search--top\" as |search|>\n      <search.open/>\n      <search.form @search={{action (mut blockQuery)}}/>\n    </NyprMInlineSearch>\n    <!-- END-SNIPPET -->",
  "block-example.hbs": "    <NyprMBlock @orientation={{this.orientation}} @size={{this.size}} as |block|>\n      <block.media\n        @url='https://example.com/story'\n        @srcS='https://picsum.photos/id/237/600/400'\n        @alt='alt text'\n        @tag={{hash label='Foo' route=(array null 'bar')}}\n      />\n\n      <block.object as |o|>\n        <o.eyebrow @text='News'/>\n\n        <o.title @h2>\n          Story Title\n        </o.title>\n\n        <o.body as |body|>\n          <body.text>\n            Short summary of the story\n          </body.text>\n\n          <body.meta\n            @authors={{array (hash name='Jen Chung')}}\n            @commentCount=46\n          />\n        </o.body>\n      </block.object>\n\n    </NyprMBlock>",
  "block-list-group-usage.hbs": "      <NyprOBlockListGroup @layoutType={{layoutType}} as |blg|>\n        <blg.col1 style=\"border: 2px solid green;\">\n          <NyprMBlockList @items={{BLOCKS}} as |item|>\n\n            <NyprMBlock @orientation='h' @size='s' as |block|>\n              <block.media @srcS={{item.thumbnail}}/>\n              <block.object as |o|>\n                <o.eyebrow @text={{item.section}}/>\n                <o.title @h3>{{item.title}}</o.title>\n                <o.body as |body|>\n                  <body.text>{{item.summary}}</body.text>\n                  <body.meta @authors={{array (hash name=item.author)}} @commentCount={{item.commentCount}}/>\n                </o.body>\n              </block.object>\n            </NyprMBlock>\n\n          </NyprMBlockList>\n        </blg.col1>\n\n        {{#unless singleColumn}}\n          <blg.col2 style=\"border: 2px solid blue;\" as |col|>\n            <col.ad>\n              Ad here\n            </col.ad>\n          </blg.col2>\n        {{/unless}}\n      </NyprOBlockListGroup>",
  "block-list-usage.hbs": "    <NyprMBlockList @items={{BLOCKS}} as |item|>\n\n      <NyprMBlock as |block|>\n        <block.media @srcS={{item.thumbnail}}/>\n\n        <block.object as |o|>\n          <o.eyebrow @text={{item.section}}/>\n\n          <o.title @h3>{{item.title}}</o.title>\n\n          <o.body as |body|>\n            <body.text>{{item.summary}}</body.text>\n            <body.meta @authors={{array (hash name=item.author)}} @commentCount={{item.commentCount}}/>\n          </o.body>\n        </block.object>\n      </NyprMBlock>\n\n    </NyprMBlockList>",
  "block-meta-example.hbs": "    <NyprMBlockMeta\n      @authors={{array (hash name='Jen Chung') (hash name='Jake Dobkin')}}\n      @commentCount=100\n    />",
  "blockquote.hbs": "    <blockquote>\n      <p>\n        Lorem ipsum\n      </p>\n    </blockquote>",
  "box-banner-example.hbs": "    <NyprMBoxBanner @close={{close}} as |banner|>\n\n        <banner.title>\n          <a href=\"\">\n            Long Wrapping Linked Headline Goes Here Now.\n          </a>\n        </banner.title>\n\n      {{#if showBody}}\n        <banner.body as |body|>\n          <body.dek>\n            <p>\n              A coalition of community groups are <a href=\"\">suing the city</a> to stop a multi-billion dollar mega development of four high-rise towers that some locals fear will greatly.\n            </p>\n          </body.dek>\n\n          <body.cta>\n            <NyprAButton>\n              Got it, thanks.\n            </NyprAButton>\n          </body.cta>\n        </banner.body>\n      {{/if}}\n\n    </NyprMBoxBanner>",
  "brands-linkroll-example.hbs": "      <NyprMBrandsLinkroll/>",
  "breadcrumb-definition.js": "const BREADCRUMBS = [{\n  label: 'News',\n  url: '/tags/news',\n}, {\n  label: 'Politics'\n}];",
  "breadcrumb-example.hbs": "    <NyprMBreadcrumb @breadcrumbs={{BREADCRUMBS}} />",
  "button-example.hbs": "    <NyprAButton\n      @url={{url}}\n      @onclick={{action (mut clicked) true}}\n      @blank={{blank}}\n    >\n      Click Me\n    </NyprAButton>",
  "byline-choices.js": "const IN_HOUSE = [{\n  name: 'Jen Chung',\n  url: '/authors/jen-chung'\n}, {\n  name: 'Brian Lehrer',\n  url: '/authors/brian-lehrer'\n}, {\n  name: 'Beth Fertig',\n  url: '/authors/beth-fertig',\n}, {\n  name: 'Nancy Solomon',\n  url: '/authors/nancy-solomon',\n}];\n\nconst CONTRIBUTORS = [{\n  name: 'Andrea Bernstein',\n  url: '/authors/andrea-bernstein',\n  org: {\n    name: 'WNYC',\n    url: 'wnyc.org',\n  }\n}, {\n  name: 'Ariana Tobin',\n  url: '/authors/ariana-tobin',\n  org: {\n    name: 'ProPublica',\n    url: 'propublica.org',\n  }\n}, {\n  name: 'Ilya Marritz',\n  url: '/authors/ilya-marritz',\n  org: {\n    name: 'BBC',\n    url: 'bbc.co.uk'\n  }\n}, {\n  name: 'Jen Carlson',\n  url: '/authors/jen-carlson',\n  org: {\n    name: 'Gothamist',\n    url: 'gothamist.com'\n  }\n}];\n\nconst SPONSOR = {\n  name: 'BMW',\n  url: 'https://example.com/bmw',\n};",
  "byline-example.hbs": "    <NyprMByline @authors={{authors}} @sponsor={{sponsor}} />",
  "contact-example.hbs": "    <NyprOContact>\n      <strong>hello</strong> <em>world</em>\n    </NyprOContact>",
  "donate-example.hbs": "      <NyprODonate\n        @message='hello world'\n        @linkText='click me'\n        @linkUrl='http://example.com/pledge'\n        @close={{action (mut close) true}}\n      />",
  "featured-block-list-usage.hbs": "    <NyprOFeaturedBlockList as |fbl|>\n      <fbl.heading>\n        <span class=\"c-featured-blocks__heading-icon o-icon u-icon--s u-path-fill--quaternary\">\n          <NyprASvg @icon='arrow-stylish'/>\n        </span>\n        Featured\n      </fbl.heading>\n\n      <fbl.featured>\n        {{#with (get BLOCKS 0) as |item|}}\n          <NyprMBlock as |block|>\n            <block.media\n              @srcS={{item.thumbnail}}\n            />\n\n            <block.object as |o|>\n              <o.eyebrow @text={{item.section}}/>\n\n              <o.title @h3>\n                {{item.title}}\n              </o.title>\n\n              <o.body as |body|>\n                <body.text>\n                  {{item.summary}}\n                </body.text>\n\n                <body.meta\n                  @timestamp=\"1 hour ago\"\n                  @commentCount={{item.commentCount}}\n                />\n              </o.body>\n            </block.object>\n          </NyprMBlock>\n        {{/with}}\n      </fbl.featured>\n\n      <fbl.col>\n        <NyprMBlockList @items={{slice 1 undefined BLOCKS}} as |item|>\n          <NyprMBlock @orientation='h' @size='s' as |block|>\n            <block.media\n              @srcS={{item.thumbnail}}\n            />\n\n            <block.object as |o|>\n              <o.eyebrow @text={{item.section}}/>\n\n              <o.title @h3>\n                {{item.title}}\n              </o.title>\n\n              <o.body as |body|>\n                <body.meta\n                  @timestamp=\"1 hour ago\"\n                  @commentCount={{item.commentCount}}\n                />\n              </o.body>\n            </block.object>\n          </NyprMBlock>\n        </NyprMBlockList>\n      </fbl.col>\n\n    </NyprOFeaturedBlockList>\n",
  "figcaption-example-block.hbs": "    <NyprMFigcaption as |caption|>\n      <caption.credit @credit='Getty Images Block Format'/>\n\n      <h2>Here's some arbitrary content</h2>\n\n      <caption.caption @caption=\"Here's a figcaption in block form\" />\n    </NyprMFigcaption>",
  "figcaption-example.hbs": "    <NyprMFigcaption\n      @caption='This is a figcaption!'\n      @credit='Getty Images'\n    />",
  "figure-block-usage.hbs": "    <NyprMFigure as |figure|>\n      <div style=\"background-color: darkseagreen;\">\n        <figure.figcaption @caption='Caption for This Image' />\n      </div>\n\n      <figure.image @srcS='https://picsum.photos/800/400' />\n    </NyprMFigure>",
  "figure-embed-example.hbs": "    <NyprMFigure @caption=\"embed caption\" @credit=\"embed credit\">\n      <iframe width=\"100%\" height=\"664\" src=\"https://www.youtube.com/embed/pL-drozge8Q\" frameborder=\"0\" allow=\"accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture\" allowfullscreen></iframe>\n    </NyprMFigure>",
  "figure-example.hbs": "    <NyprMFigure\n      @caption='This is a figcaption!'\n      @credit='Getty Images'\n      @breakXl=1200\n      @srcXl='https://picsum.photos/1200/800'\n      @breakL=1000\n      @srcL='https://picsum.photos/1000/600'\n      @breakM=800\n      @srcM='https://picsum.photos/800/500'\n      @breakS=600\n      @srcS='https://picsum.photos/600/400'\n      @alt='Pass in an alt tag'\n    />",
  "foo": "\n    <p>\n      <button class=\"btn\" {{action (mut singleColumn) (not singleColumn)}}>\n        Show as {{if singleColumn 'Two Column' 'Single Column'}}\n      </button>\n    </p>\n    <aside>Columns are declarative. Simply add the column to your template or not</aside>\n\n    <p>\n      <label>\n        <input name=\"layout\" type=\"radio\" onclick={{action (mut layoutType) 'right-rail'}} checked>\n        Right Rail\n      </label>\n      <label>\n        <input name=\"layout\" type=\"radio\" onclick={{action (mut layoutType) 'two-up'}}>\n        Two Up\n      </label>\n      <label>\n        <input name=\"layout\" type=\"radio\" onclick={{action (mut layoutType) 'offset'}}>\n        Offset\n      </label>\n    </p>\n    <p><code>@layoutType</code>: <strong>{{or layoutType 'right-rail'}}</strong></p>\n    <aside>\n      <code>right-rail</code> is the defualt. Green and blue borders added for reference.\n    </aside>\n\n    <!-- BEGIN-SNIPPET block-list-group-usage.hbs -->\n      <NyprOBlockListGroup @layoutType={{layoutType}} as |blg|>\n        <blg.col1 style=\"border: 2px solid green;\">\n          <NyprMBlockList @items={{BLOCKS}} as |item|>\n\n            <NyprMBlock @orientation='h' @size='s' as |block|>\n              <block.media @srcS={{item.thumbnail}}/>\n              <block.object as |o|>\n                <o.eyebrow @text={{item.section}}/>\n                <o.title @h3>{{item.title}}</o.title>\n                <o.body as |body|>\n                  <body.text>{{item.summary}}</body.text>\n                  <body.meta @authors={{array (hash name=item.author)}} @commentCount={{item.commentCount}}/>\n                </o.body>\n              </block.object>\n            </NyprMBlock>\n\n          </NyprMBlockList>\n        </blg.col1>\n\n        {{#unless singleColumn}}\n          <blg.col2 style=\"border: 2px solid blue;\" as |col|>\n            <col.ad>\n              Ad here\n            </col.ad>\n          </blg.col2>\n        {{/unless}}\n      </NyprOBlockListGroup>\n    <!-- END-SNIPPET -->",
  "footer-example.hbs": "      <NyprOFooter as |footer|>\n        <footer.slogan>\n          <p>\n            Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco.\n          </p>\n        </footer.slogan>\n\n        <footer.nav\n          @navItems={{array\n            (hash url='/advertising' title='Advertising')\n            (hash url='/contact' title='Contact Us')\n            (hash url='/newsletter' title='Newsletter')\n          }}\n        />\n\n        <footer.signup>\n          <NyprMNewsletter as |newsletter|>\n            <newsletter.graphic>\n              <NyprASvg @icon='party-confetti'/>\n            </newsletter.graphic>\n            <newsletter.blurb>Sign up today</newsletter.blurb>\n\n            <newsletter.legal/>\n          </NyprMNewsletter>\n        </footer.signup>\n\n      </NyprOFooter>",
  "gallery-lead-example.hbs": "    <NyprMGalleryLead @slides={{GALLERY}}/>",
  "gallery-usage.hbs": "    <NyprOGalleryOverlay\n      @title=\"Gallery Title\"\n      @takeover={{false}}\n      @slides={{SLIDES}}\n      @viewedSlide={{action (mut viewed)}}>\n    </NyprOGalleryOverlay>",
  "header-example.hbs": "      <NyprOHeader\n        @donateURL={{DONATE_URL}}\n        as |header|>\n        {{#unless (or STORY GALLERY)}}\n          <header.leaderboard>\n            <picture>\n              <source media=\"(min-width: 750px)\" srcset=\"https://tpc.googlesyndication.com/simgad/3866766746825522936\">\n              <img src=\"https://s0.2mdn.net/5188901/TINCUP_OUTSIDEONLINE_300x50.jpg\" alt=\"\">\n            </picture>\n          </header.leaderboard>\n        {{/unless}}\n\n        <header.menu as |menu|>\n          <menu.branding>\n            <NyprASvg @icon='gothamist-logo'/>\n          </menu.branding>\n\n          <menu.primaryNav @navItems={{PRIMARY_NAV_ITEMS}} />\n\n          <menu.secondaryNav @navItems={{SECONDARY_NAV_ITEMS}} />\n        </header.menu>\n\n        <header.left as |left|>\n          <left.branding>\n            <a href=\"http://gothamist.com\">\n              <NyprASvg @icon='gothamist-logo'/>\n            </a>\n          </left.branding>\n\n          {{#if STORY}}\n            <left.headline>\n              {{STORY.title}}\n            </left.headline>\n          {{/if}}\n\n        </header.left>\n\n        {{#unless (or STORY GALLERY)}}\n          <header.nav @navItems={{PRIMARY_NAV_ITEMS}}/>\n        {{/unless}}\n\n        <header.right as |right|>\n          {{#if STORY}}\n            <NyprMShareTools @label='Share' as |tools|>\n              <tools.share @service='facebook' />\n              <tools.share @service='twitter' @params={{hash text='Read this great article' via='WNYC'}}/>\n            </NyprMShareTools>\n\n            <NyprAButton\n              class='c-main-header__donate o-button--sm'\n              @url={{DONATE_URL}}>\n              Donate\n            </NyprAButton>\n\n            <right.search as |search|>\n              <search.open/>\n              <search.form/>\n            </right.search>\n\n          {{else if GALLERY}}\n            <NyprMShareTools @label='Share' as |tools|>\n              <tools.share @service='facebook' />\n              <tools.share @service='twitter' @params={{hash text='Read this great article' via='WNYC'}}/>\n            </NyprMShareTools>\n\n            <NyprAButton @blank={{true}} class=\"u-padding--half\">\n              <NyprASvg @icon='close' class=\"c-main-header__close\"/>\n            </NyprAButton>\n          {{else}}\n\n            <NyprAButton\n              class='c-main-header__donate o-button--sm'\n              @url={{DONATE_URL}}>\n              Donate\n            </NyprAButton>\n\n            <right.search as |search|>\n              <search.open/>\n              <search.form/>\n            </right.search>\n\n          {{/if}}\n\n        </header.right>\n      </NyprOHeader>",
  "kicker-usage.hbs": "    <NyprAKicker>\n      Category\n    </NyprAKicker>",
  "media-object-basic.hbs": "    <NyprAMediaObject as |mo|>\n      <mo.media>\n        <img src=\"https://via.placeholder.com/150\" />\n      </mo.media>\n\n      <mo.object>\n        The humble media object.\n      </mo.object>\n    </NyprAMediaObject>",
  "newsletter-form-usage.hbs": "    <NyprMNewsletterForm @endpoint={{@endpoint}} @params={{@params}} as |form|>\n      <form.label/>\n      <form.input/>\n\n      <form.submit>\n        <NyprASvg @icon='arrow-dashed' style=\"fill: #fff\" width=\"25\" />\n      </form.submit>\n\n      <form.legal/>\n    </NyprMNewsletterForm>",
  "newsletter-tout-usage.hbs": "    <NyprONewsletterTout\n      @img={{hash\n        src='https://picsum.photos/500/500?image=66'\n        alt='newsletter image'\n      }}\n      as |tout|>\n      <tout.heading>\n        Sign Up for We The Commuters\n      </tout.heading>\n\n      <tout.blurb>\n        You already read our words, now you can read them in email form too! Read a quick bite-sized list of content!\n      </tout.blurb>\n\n      <tout.legal/>\n    </NyprONewsletterTout>\n",
  "newsletter-usage.hbs": "\n    <NyprMNewsletter\n      @endpoint='http://api.demo.nypr.digital/opt-in/v1/subscribe/mailchimp'\n      @params={{hash list=\"058457038f\"}}\n      as |newsletter|>\n\n      <newsletter.graphic>\n        <NyprASvg @icon='party-confetti'/>\n      </newsletter.graphic>\n\n      <newsletter.blurb>\n        Sign up for this great newsletter.\n      </newsletter.blurb>\n\n      <newsletter.legal/>\n\n    </NyprMNewsletter>\n",
  "nypr-a-ad-label.hbs": "Advertisement",
  "nypr-a-ad-label.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-a-ad-label';\n\n/**\n  Advertising label\n\n  @class nypr-a-ad-label\n*/\nexport default Component.extend({\n  layout,\n  tagName: 'span',\n  classNames: ['o-ad__label', 'u-font--secondary-style', 'u-font--xxs'],\n});",
  "nypr-a-button.hbs": "{{#if (or @url @route)}}\n  {{#if @url}}\n    <a\n      href={{@url}}\n      target=\"_blank\"\n      rel=\"noopener\"\n      class=\"{{unless @blank 'o-button'}} {{this.tierClass}} {{@class}}\"\n      ...attributes>\n      {{#if (has-block)}}\n        {{yield}}\n      {{else}}\n        {{@text}}\n      {{/if}}\n    </a>\n  {{else}}\n    {{#link-to\n      params=@route\n      class=(concat (unless @blank 'o-button') this.tierClass @class)\n    }}\n      {{#if (has-block)}}\n        {{yield}}\n      {{else}}\n        {{@text}}\n      {{/if}}\n    {{/link-to}}\n  {{/if}}\n{{else}}\n  <button\n    class=\"{{unless @blank 'o-button'}} {{this.tierClass}} {{@class}}\"\n    role={{or @role this.role}}\n    type={{or @type this.type}}\n    onClick={{optional @onclick}}\n    ...attributes>\n\n    {{#if (has-block)}}\n      {{yield}}\n    {{else}}\n      {{@text}}\n    {{/if}}\n\n  </button>\n{{/if}}",
  "nypr-a-button.js": "import Component from '@ember/component';\nimport { computed } from '@ember/object';\nimport layout from '../templates/components/nypr-a-button';\n\n/**\n  Base button. Use this component to compose more specific buttons and components.\n\n  If a `@url` is passed, an `a` tag is rendered with `target=\"_blank\" rel=\"noopener\"`\n\n  Usage:\n  ```hbs\n  <NyprAButton>\n    Click Me\n  </NyprAButton>\n  ```\n\n  @class nypr-a-button\n  @yield {Block} block\n*/\nexport default Component.extend({\n  layout,\n  tagName: '',\n\n  /**\n    Make this a plain, unstyled button. Useful for wrapping icons\n\n    @argument blank\n    @type {Boolean}\n  */\n\n  /**\n    Specify a click handler\n\n    @argument onclick\n    @type {Function}\n  */\n\n  /**\n    Specify the `role` attribute. Default is 'button'.\n    @argument role\n    @type {String}\n    @default 'button'\n  */\n  role: 'button',\n  /**\n    Text for element contents\n\n    @argument text\n    @type {String}\n  */\n  text: null,\n\n  /**\n    Which tier in the visual heirarchy this button should be.\n\n    @argument tier\n    @type {String}\n  */\n  tier: null,\n\n  /**\n    Specify the `type` attribute for the button. Default is \"button\".\n    @argument type\n    @type {String?}\n    @default 'button'\n  */\n  type: 'button',\n\n  /**\n    External URL\n\n    @argument url\n    @type {String}\n  */\n  url: null,\n\n  /**\n    Route arguments for `link-to` component.\n\n    [`<route name>`, `<model or identifier>`]\n\n    @argument route\n    @type {Array}\n  */\n\n  /**\n    Computes the class applied based on the value of `tier`\n\n    @accessor tierClass\n    @type {String}\n  */\n  tierClass: computed('tier', function() {\n    return this.tier ? `o-button--${this.tier}` : '';\n  }),\n});",
  "nypr-a-kicker.hbs": "{{#if @url}}\n  <a\n    href={{@url}}\n    target=\"_blank\"\n    rel=\"noopener\"\n    class=\"o-kicker\"\n    ...attributes>\n    {{#if (has-block)}}\n      {{yield}}\n    {{else}}\n      {{@text}}\n    {{/if}}\n  </a>\n{{else if @route}}\n  <LinkTo\n    @params={{@route}}\n    class='o-kicker'\n    ...attributes\n  >\n    {{#if (has-block)}}\n      {{yield}}\n    {{else}}\n      {{@text}}\n    {{/if}}\n  </LinkTo>\n{{else}}\n  <span class=\"o-kicker\" ...attributes>\n    {{#if (has-block)}}\n      {{yield}}\n    {{else}}\n      {{@text}}\n    {{/if}}\n  </span>\n{{/if}}",
  "nypr-a-kicker.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-a-kicker';\n\n/**\n  Render a \"kicker\" or small piece of decorated text.\n\n  Usage:\n  ```hbs\n  <NyprAKicker>\n    News\n  </NyprAKicker>\n  ```\n  @class nypr-a-kicker\n  @yield {Block} block\n*/\nexport default Component.extend({\n  layout,\n  tagName: '',\n\n  /**\n    Text contents.\n    @argument text\n    @type {String}\n  */\n\n  /**\n    Arguments to pass to a link-to component\n\n    @argument route\n    @type {Array}\n  */\n\n  /**\n    URL for anchor tag\n\n    @argument url\n    @type {String}\n  */\n});",
  "nypr-a-media-object.hbs": "<div class=\"media-object__media\">\n  {{yield (hash media=(component 'blank-template'))}}\n</div>\n\n<div class=\"media-object__object\">\n  {{yield (hash object=(component 'blank-template'))}}\n</div>",
  "nypr-a-media-object.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-a-media-object';\n\n/**\n  The OOCSS Media Object [popularized by Nicole Sullivan](http://www.stubbornella.org/content/2010/06/25/the-media-object-saves-hundreds-of-lines-of-code/)\n\n  Usage:\n  ```hbs\n  <NyprAMediaObject as |mo|>\n    <mo.media>\n      Media area content, like an image\n    </mo.media>\n\n    <mo.object>\n      Text content or other component\n    </mo.object>\n  </NyprAMediaObject>\n  ```\n  @class nypr-a-media-object\n  @yield {Hash} mo\n  @yield {Component} mo.media Yields a block\n  @yield {Component} mo.object Yields a block\n*/\nexport default Component.extend({\n  layout,\n  classNames: ['media-object'],\n  classNameBindings: ['valign'],\n\n  /**\n    Specify a vertical alignment.\n    Accepts `'t'`, `'c'`, `'b'` for top, center, and bottom.\n\n    @argument valign\n    @type {String?}\n    @default null\n  */\n  valign: null,\n});",
  "nypr-a-picture.hbs": "{{#if @breakXl}}\n  <source\n    data-test-source-xl\n    srcset={{@srcXl}}\n    media=\"(min-width: {{@breakXl}}px)\"\n  >\n{{/if}}\n\n{{#if @breakL}}\n  <source\n    data-test-source-l\n    srcset={{@srcL}}\n    media=\"(min-width: {{@breakL}}px)\"\n  >\n{{/if}}\n\n{{#if @breakM}}\n  <source\n    data-test-source-m\n    srcset={{@srcM}}\n    media=\"(min-width: {{@breakM}}px)\"\n  >\n{{/if}}\n\n{{#if @lazy}}\n  <img\n    class=\"lazy\"\n    src=\"data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==\"\n    data-src={{@srcS}}\n    itemprop=\"image\"\n    alt={{@alt}}\n    width={{@width}}\n    height={{@height}}\n    srcset={{@srcSet}}\n  >\n{{else}}\n  <img\n    src={{@srcS}}\n    itemprop=\"image\"\n    alt={{@alt}}\n    width={{@width}}\n    height={{@height}}\n    srcset={{@srcSet}}\n  >\n{{/if}}",
  "nypr-a-picture.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-a-picture';\nimport { computed } from '@ember/object';\n\n/**\n  Picture element. Renders `<source>` elements for provided s, m, l, or xl breakpoints and images.\n\n  Usage:\n  ```hbs\n  <NyprAPicture @breakXl=1000 @srcXl='https://example.com/xlarge-img.jpg' @breakL=800 @srcL='https://example.com/large-img.jpg' @alt='Descriptive alt text'/>\n  ```\n  @class nypr-a-picture\n*/\nexport default Component.extend({\n  tagName: 'picture',\n  classNames: ['o-picture'],\n  classNameBindings: ['ratioClass'],\n  layout,\n\n  ratioClass: computed('ratio', function() {\n    if (this.ratio) {\n      return `u-img-ratio--${this.ratio}`;\n    }\n  }),\n\n  /**\n    Extra Large Breakpoint\n    @argument breakXl\n    @type {Number}\n  */\n  breakXl: null,\n\n  /**\n    Image url for corresponding extra large breakpoint\n    @argument srcXl\n    @type {String}\n  */\n  srcXl: null,\n\n  /**\n    Large Breakpoint\n    @argument breakL\n    @type {Number}\n  */\n  breakL: null,\n\n  /**\n    Image url for corresponding large breakpoint\n    @argument srcL\n    @type {String}\n  */\n  srcL: null,\n\n  /**\n    Medium Breakpoint\n    @argument breakM\n    @type {Number}\n  */\n  breakM: null,\n\n  /**\n    Image url for corresponding medium breakpoint\n    @argument srcM\n    @type {String}\n  */\n  srcM: null,\n\n  /**\n    Small Breakpoint\n    @argument breakS\n    @type {String}\n  */\n  breakS: null,\n\n  /**\n    Image url for corresponding small breakpoint\n    @argument srcS\n    @type {String}\n  */\n  srcS: null,\n\n  /**\n    Should this image lazy load or not?\n    @argument lazy\n    @type {Boolean}\n  */\n  lazy: false,\n\n  /**\n    Text for `alt` attribute.\n    @argument alt\n    @type {String}\n  */\n  alt: null,\n\n  /**\n    Width for image element\n    @argument width\n    @type {Number}\n  */\n\n  /**\n    Height for image element\n    @argument height\n    @type {Number}\n  */\n});",
  "nypr-a-section-heading.hbs": "{{#if (has-block)}}\n  {{yield}}\n{{else}}\n  {{@text}}\n{{/if}}",
  "nypr-a-section-heading.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-a-section-heading';\n\n/**\n  Section header markup\n\n  Usage:\n  ```hbs\n  <NyprASectionHeading>\n    Section Header Goes Here\n  </NyprASectionHeading>\n  ```\n  @class nypr-a-section-heading\n  @yield {Block} block\n*/\nexport default Component.extend({\n  layout,\n  tagName: 'h2',\n  classNames: ['o-section-heading'],\n\n  /**\n    Text contents.\n    @argument text\n    @type {String?}\n  */\n  text: null,\n});",
  "nypr-a-story-headline.hbs": "{{#if (has-block)}}\n  {{yield}}\n{{else}}\n  {{@text}}\n{{/if}}",
  "nypr-a-story-headline.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-a-story-headline';\n\n/**\n  Headline markup for stories.\n\n  Usage:\n  ```hbs\n  <NyprAStoryHeadline>\n    Headline goes here\n  </NyprAStoryHeadline>\n  ```\n  @class nypr-a-story-headline\n  @yield {Block} block\n*/\nexport default Component.extend({\n  layout,\n  tagName: 'h1',\n  classNames: ['o-headline'],\n\n  /**\n    Text contents.\n    @argument text\n    @type {String?}\n  */\n  text: null,\n});",
  "nypr-a-svg.js": "import Component from '@ember/component';\nimport { computed } from '@ember/object';\nimport { getOwner } from '@ember/application';\n\nconst msg = icon =>\n  `Could not find an icon in nypr-design-system/app/templates/components/nypr-a-svg named ${icon}`;\n\n/**\n  A component that will safely render any svg icon found in the `app/templates/components/nypr-a-svg` tree.\n\n  If an svg template can't be found, a warning will be printed.\n\n  Usage:\n  ```hbs\n  <NyprASvg @icon='wnyc-logo'/>\n  ```\n\n  @class nypr-a-svg\n*/\nexport default Component.extend({\n  tagName: '',\n\n  /**\n    Filename of the icon to render.\n\n    @argument icon\n    @type {String}\n  */\n  icon: null,\n\n  /**\n    Looks up the svg template\n\n    @accessor svgPartial\n  */\n  svgPartial: computed(function() {\n    let owner = getOwner(this);\n    let templateExists = Boolean(owner.lookup(`templates/component:nypr-a-svg/${this.icon}`));\n    if (templateExists) {\n      return `components/nypr-a-svg/${this.icon}`;\n    } else {\n      console.warn(msg(this.icon)); // eslint-disable-line\n      return false;\n    }\n  }),\n});",
  "nypr-a-tag.hbs": "{{#if @url}}\n  <a\n    href={{@url}}\n    target=\"_blank\"\n    rel=\"noopener\"\n    class=\"o-tag\"\n    ...attributes>\n    {{or @prefix this.prefix}}{{@tag}}\n  </a>\n{{else if @route}}\n  {{#link-to\n    params=@route\n    class='o-tag'\n  }}\n    {{or @prefix this.prefix}}{{@tag}}\n  {{/link-to}}\n{{else}}\n  <span class=\"o-tag\">{{or @prefix this.prefix}}{{@tag}}</span>\n{{/if}}",
  "nypr-a-tag.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-a-tag';\n\n/**\n  Show a tag\n\n  Usage:\n  ```hbs\n    <NyprATag @tag='foo'/>\n  ```\n\n  @class nypr-a-tag\n*/\nexport default Component.extend({\n  layout,\n  tagName: '',\n\n  /**\n    Prefix the rendered tag with a value. Defaults to `\"#\"`.\n    Set to `''` to blank out.\n\n    @argument prefix\n    @type {String}\n  */\n  prefix: '#',\n\n  /**\n    Tag text to display\n\n    @argument tag\n    @type {String}\n  */\n  tag: null,\n\n  /**\n    Set the URL for this tag.\n    If it's unset, the root element is a span. If it is set, this is an anchor tag.\n\n    @argument url\n    @type {String}\n  */\n  url: null,\n});",
  "nypr-a-toggle-box.hbs": "{{#basic-dropdown\n  calculatePosition=@calculatePosition\n  horizontalPosition=@horizontalPosition\n  verticalPostion=@verticalPostion\n  renderInPlace=@renderInPlace\n  onOpen=(action (mut this.open) true)\n  onClose=(action (mut this.open) false)\n  as |dd|}}\n  {{yield (hash\n    trigger=(component 'nypr-a-toggle-box/trigger' button=dd.trigger icon=icon)\n    dropdown=(component dd.content class=\"c-toggle-box__dropdown\")\n  )}}\n{{/basic-dropdown}}",
  "nypr-a-toggle-box.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-a-toggle-box';\n\n/**\n  @class nypr-a-toggle-box\n  @yield {Block} block\n*/\nexport default Component.extend({\n  layout,\n\n  classNames: ['c-toggle-box'],\n  classNameBindings: ['open:this-is-active'],\n\n  open: false,\n\n  /**\n    Icon trigger\n\n    @argument icon\n    @type {String}\n  */\n  icon: 'caret-down',\n});",
  "nypr-header-o-left.js": "import Component from '@ember/component';\nimport layout from '../../templates/components/nypr-o-header/left';\n\nexport default Component.extend({\n  layout,\n  classNames: ['c-main-header__left', '_u-truncate--flex']\n});",
  "nypr-m-author-block.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-m-author-block';\n\n/**\n  Display author information\n\n  @class nypr-m-author-block\n*/\nexport default Component.extend({\n  layout,\n  classNames: ['c-author-block'],\n\n  /**\n    Author object. Keys required:\n    - name\n    - title\n    - image\n    - bio\n    - contact: each key is a service where the value is the username\n\n    @argument author\n    @type {Object}\n  */\n});",
  "nypr-m-block-meta.hbs": "{{#if @authors}}\n  <span class=\"c-block-meta__author u-font--secondary-style u-font--xxs\">\n    {{#each @authors as |author|}}\n      {{~#if (and (not (has-next author @authors)) (gt @authors.length 1))~}}\n        {{! the linter doesn't count the encoded space as a character when it comes to indentation }}\n       &nbsp;and&nbsp;\n      {{~else if (and (has-next author @authors) (has-previous author @authors))~}}\n        ,&nbsp;\n      {{~/if~}}\n\n      {{author.name}}\n    {{/each}}\n  </span>\n{{/if}}\n{{#if @timestamp}}\n  <span class=\"c-block-meta__timestamp u-font--secondary-style u-font--xxs\" data-test-timestamp>\n    {{@timestamp}}\n  </span>\n{{/if}}\n{{#if @commentCount}}\n  {{#if @commentRoute}}\n    {{#link-to params=@commentRoute class=\"c-block-meta__comments u-font--secondary-style u-font--xs\"}}\n      <span class=\"o-icon o-icon--comment u-icon--xxs\">\n        <NyprASvg @icon='comment'/>\n      </span>\n      {{@commentCount}}\n    {{/link-to}}\n  {{else if @commentURL}}\n    <a\n      href={{@commentURL}}\n      class=\"c-block-meta__comments u-font--secondary-style u-font--xs\"\n      target=\"_blank\"\n      rel=\"noopener\"\n    >\n      <span class=\"o-icon o-icon--comment u-icon--xs\">\n        <NyprASvg @icon='comment'/>\n      </span>\n      {{@commentCount}}\n    </a>\n  {{/if}}\n{{/if}}",
  "nypr-m-block-meta.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-m-block-meta';\n\n/**\n  Display author name and comment count\n\n  Usage:\n  ```hbs\n  <NyprMBlockMeta @authors={{@authors}} @commentCount={{@commentCount}} @commentRoute={{array 'comments'}}/>\n  ```\n\n  @class nypr-m-block-meta\n*/\nexport default Component.extend({\n  layout,\n  classNames: ['c-block-meta', 'u-color--meta'],\n\n  /**\n    Author list\n\n    @argument authors\n    @type {Array[Object{name String}]}\n  */\n\n  /**\n    Comment count\n\n    @argument commentCount\n    @type {Number}\n  */\n\n  /**\n    Array of params passed to `link-to` to construct a url to the comments section\n\n    @argument commentRoute\n    @type {Array[route String, models Object|Array]}\n  */\n\n  /**\n    If passed in, a plain old anchor will be rendered instead of a `link-to`\n\n    @argument commentURL\n    @type {String}\n  */\n\n\n});",
  "nypr-m-block.hbs": "{{yield (hash\n  title=(component 'nypr-m-block/title')\n  media=(component 'nypr-m-block/media')\n  object=(component 'nypr-m-block/object')\n)}}",
  "nypr-m-block.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-m-block';\nimport { computed } from '@ember/object';\n\n/**\n  Display story details\n\n  Usage:\n  ```hbs\n  <NyprMBlock @orientation={{orientation}} @size={{size}} as |block|>\n    <block.media @route={{array 'story' 'slug'}} @srcS='https://picsum.photos/800/450' @alt='alt text' />\n\n    <block.object as |o|>\n      <o.eyebrow @text='News'/>\n\n      <o.title @h2 @route={{array 'story' 'slug'}}>\n        Story Title\n      </o.title>\n\n      <o.body as |body|>\n        <body.text>\n          Short summary of the story\n        </body.text>\n\n        <body.meta @authors={{array (hash name='Jen Chung')}} @commentCount=46 />\n      </o.body>\n    </block.object>\n\n  </NyprMBlock>\n  ```\n\n  @class nypr-m-block\n  @yield {Hash} hash\n  @yield {Component} title `nypr-m-block/title`\n  @yield {Component} media `nypr-m-block/media`\n  @yield {Hash} object `nypr-m-block/object`\n  @yield {Component} object.eyebrow `nypr-m-block/eyebrow`\n  @yield {Component} object.title `nypr-m-block/title`\n  @yield {Hash} object.body `nypr-m-block/body`\n  @yield {Component} object.body.text `nypr-m-block/text`\n  @yield {Component} object.body.meta `nypr-m-block-meta`\n*/\nexport default Component.extend({\n  layout,\n  classNames: ['c-block'],\n  classNameBindings: ['orientationClass', 'sizeClass', 'urgent:c-block--urgent'],\n\n  /**\n    Layout orientation.\n    One of `'v'` or `'h'` for vertical or horizontal.\n\n    @argument orientation\n    @type {String}\n  */\n  orientation: 'v',\n\n  /**\n    Block size.\n    One of `'l'` or `'s'`\n\n    @argument size\n    @type {String}\n  */\n  size: 'l',\n\n  /**\n    Compute orientation class based on `'orientation'`\n\n    @accessor orientationClass\n    @type {String}\n  */\n  orientationClass: computed('orientation', function() {\n    switch(this.orientation) {\n      case 'h':\n        return 'c-block--horizontal';\n      case 'v':\n        return null;\n    }\n  }),\n\n  /**\n    Compute size class based on `'size'`\n\n    @accessor sizeClass\n    @type {String}\n  */\n  sizeClass: computed('size', 'orientation', function() {\n    switch(this.size) {\n      case 'l':\n        return this.orientation === 'h' ? 'c-block--horizontal--large' : null;\n      case 's':\n        // not implemented\n        return;\n      default:\n        return null;\n    }\n  }),\n});",
  "nypr-m-box-banner-body.hbs": "{{yield (hash\n  dek=(component 'nypr-m-box-banner/dek')\n  cta=(component 'nypr-m-box-banner/cta')\n)}}",
  "nypr-m-box-banner-body.js": "import Component from '@ember/component';\nimport layout from '../../templates/components/nypr-m-box-banner/body';\n\nexport default Component.extend({\n  layout,\n  classNames: ['o-box-banner__group', 'u-spacing']\n});",
  "nypr-m-box-banner-cta.hbs": "{{yield}}",
  "nypr-m-box-banner-cta.js": "import Component from '@ember/component';\nimport layout from '../../templates/components/nypr-m-box-banner/cta';\n\nexport default Component.extend({\n  layout,\n  classNames: ['o-box-banner__cta'],\n});",
  "nypr-m-box-banner-dek.hbs": "{{yield}}",
  "nypr-m-box-banner-dek.js": "import Component from '@ember/component';\nimport layout from '../../templates/components/nypr-m-box-banner/dek';\n\nexport default Component.extend({\n  layout,\n  classNames: ['o-box-banner__dek', 'o-rte-text'],\n});",
  "nypr-m-box-banner-title.hbs": "{{yield}}",
  "nypr-m-box-banner-title.js": "import Component from '@ember/component';\nimport layout from '../../templates/components/nypr-m-box-banner/title';\n\nexport default Component.extend({\n  layout,\n  tagName: 'h3',\n  classNames: ['o-box-banner__title'],\n});",
  "nypr-m-box-banner.hbs": "{{#if @close}}\n  <NyprAButton\n    class=\"o-box-banner__close\"\n    @blank={{true}}\n    @onclick={{@close}}\n  >\n    <span class=\"o-icon u-icon--xs o-icon--close\">\n      <NyprASvg @icon='close'/>\n    </span>\n  </NyprAButton>\n{{/if}}\n<div class=\"o-box-banner__inner l-container l-container--16col\">\n  {{yield (hash\n    title=(component 'nypr-m-box-banner/title')\n    body=(component 'nypr-m-box-banner/body')\n  )}}\n</div>",
  "nypr-m-box-banner.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-m-box-banner';\n\n/**\n  A box for rendering arbitrary content into `title` and `body` areas.\n\n  Will render a `close` button if a `@close` argument is passed.\n\n  @class nypr-m-box-banner\n  @yield {Block} block\n  @yield {Component} block.title `nypr-m-box-banner/title`\n  @yield {Component} block.dek `nypr-m-box-banner/dek`\n  @yield {Component} block.cta `nypr-m-box-banner/cta`\n*/\nexport default Component.extend({\n  layout,\n\n  classNames: ['o-box-banner', 'u-spacing', 'o-3d-heading', 'o-3d-heading--quinary'],\n\n  /**\n    Function to call when the close button is clicked.\n\n    @argument close\n    @type {Function}\n  */\n});",
  "nypr-m-brands-linkroll.hbs": "<NyprAToggleBox class=\"c-toggle-box--linkroll\" @calculatePosition={{this.calculatePosition}} as |toggle|>\n  <toggle.trigger>\n    <strong>View All Brands</strong>\n  </toggle.trigger>\n\n  <toggle.dropdown>\n    <nav class=\"c-nypr-nav\" role=\"navigation\" aria-label=\"NYPR brands navigation\">\n      <ul class=\"c-nypr-nav__list\">\n        {{#each this.items as |item|}}\n          <li class=\"c-nypr-nav__item\">\n            <a\n              href={{item.url}}\n              class=\"c-nypr-nav__link u-has-accent u-has-accent--secondary\"\n              target=\"_blank\"\n              rel=\"noopener\"\n            >\n              {{item.title}}\n            </a>\n          </li>\n        {{/each}}\n      </ul>\n    </nav>\n  </toggle.dropdown>\n</NyprAToggleBox>",
  "nypr-m-brands-linkroll.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-m-brands-linkroll';\nimport { computed } from '@ember/object';\n\nimport { positionBottomCenter } from '../utils/toggle-box-positions';\n\nconst NAV_ITEMS = [{\n  url: 'https://www.wnyc.org',\n  title: 'WNYC',\n}, {\n  url: 'https://www.newsounds.org',\n  title: 'New Sounds',\n}, {\n  url: 'https://www.wqxr.org',\n  title: 'WQXR',\n}, {\n  url: 'https://gothamist.com',\n  title: 'Gothamist',\n}, {\n  url: 'https://www.njpr.org',\n  title: 'NJPR',\n}, {\n  url: 'https://www.wnycstudios.org',\n  title: 'WNYC Studios',\n}, {\n  url: 'https://www.thegreenespace.org',\n  title: 'The Greene Space',\n}];\n\n/**\n  Extends `nypr-a-toggle-box` to make a flyout with links to NYPR properties.\n\n  @class nypr-m-brands-linkroll\n*/\nexport default Component.extend({\n  layout,\n  tagName: '',\n\n  /**\n    Navigation items objects with `url` and `title` keys.\n\n    @argument navItems\n    @type {Array<Object>}\n  */\n  navItems: NAV_ITEMS,\n\n  /**\n    Choose a nav item to exclude. Will coerce to lowercase and compare with\n    each objects lowercased `title` key.\n\n    @argument exclude\n    @type {String}\n  */\n\n  items: computed('navItems', 'exclude', function() {\n    let { navItems, exclude } = this;\n    if (!navItems || navItems.length === 0) {\n      return [];\n    }\n\n    if (exclude && typeof exclude === 'string') {\n      return navItems.filter(item => item.title.toLowerCase() !== exclude.toLowerCase());\n    } else if (Array.isArray(exclude)) {\n      exclude = exclude\n        .map(e => typeof e === 'string' && e.toLowerCase())\n        .filter(e => !!e);\n      return navItems.filter(item => !exclude.includes(item.title.toLowerCase()));\n    } else {\n      return navItems;\n    }\n  }),\n\n  calculatePosition(trigger, content) {\n    return positionBottomCenter(trigger, content, 15);\n  }\n});",
  "nypr-m-breadcrumb.hbs": "{{#each @breadcrumbs as |breadcrumb|}}\n  {{#if breadcrumb.route}}\n    {{#link-to params=breadcrumb.route class=\"o-breadcrumb\"}}\n      {{breadcrumb.label}}\n    {{/link-to}}\n  {{else if breadcrumb.url}}\n    <a\n      href={{breadcrumb.url}}\n      class=\"o-breadcrumb\"\n      target=\"_blank\"\n      rel=\"noopener\">\n      {{breadcrumb.label}}\n    </a>\n  {{else}}\n    <span class=\"o-breadcrumb\">{{breadcrumb.label}}</span>\n  {{/if}}\n{{/each}}",
  "nypr-m-breadcrumb.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-m-breadcrumb';\n\n/**\n  Display a given list of breadcrumbs.\n\n  Usage:\n  ```hbs\n  <NyprMBreadcrumb @breadcrumbs={{breadcrumbs}} />\n  ```\n\n  @class nypr-m-breadcrumb\n*/\nexport default Component.extend({\n  layout,\n  classNames: ['o-breadcrumbs'],\n\n  /**\n    List of `breadcrumb` objects, with a `label` and `url` key.\n    If there is no `url` value, a span will be rendered.\n\n    @argument breadcrumbs\n    @type {Array<Object>}\n  */\n\n});",
  "nypr-m-byline.hbs": "{{#if @authors}}\n  By\n  {{#each @authors as |author|}}\n    {{~#if (and (not (has-next author @authors)) (gt @authors.length 1))~}}\n     &nbsp;and&nbsp;\n    {{~else if (and (has-next author @authors) (has-previous author @authors))~}}\n      ,&nbsp;\n    {{~/if~}}\n    {{#if author.route}}\n      {{#link-to params=author.route class=\"u-has-accent u-has-accent--neutral\" data-test-author-name=author.name}}\n        {{author.name}}\n      {{/link-to}}\n    {{else}}\n      <a\n        href={{author.url}}\n        target=\"_blank\"\n        rel=\"noopener\"\n        class=\"u-has-accent u-has-accent--neutral\"\n        data-test-author-name\n      >\n        {{author.name}}\n      </a>\n    {{/if}}\n    {{~#with author.org as |org|~}}\n      {{#if org.route}}\n        , ({{#link-to params=org.route class=\"u-has-accent u-has-accent--neutral\" data-test-author-org=org.name}}<em>{{org.name}}</em>{{/link-to}})\n      {{else}}\n        , (<a href={{org.url}} target=\"_blank\" rel=\"noopener\" class=\"u-has-accent u-has-accent--neutral\" data-test-author-org><em>{{org.name}}</em></a>)\n      {{/if}}\n    {{~/with~}}\n  {{/each}}\n{{else if @sponsor}}\n  <a href={{@sponsor.url}} target=\"_blank\" rel=\"noopener\" data-test-sponsor-name>Sponsored by {{@sponsor.name}}</a>\n{{/if}}",
  "nypr-m-byline.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-m-byline';\n\n/**\n  Byline for articles to display authors, orgs, and sponsors\n\n  Usage:\n  ```hbs\n  <NyprMByline @authors={{authors}} @sponsor={{sponsor}}/>\n  ```\n  @class nypr-m-byline\n*/\nexport default Component.extend({\n  tagName: 'span',\n  classNames: ['o-byline'],\n  layout,\n\n  /**\n    Array of Author objects with a `name`, `url`, and optional `org` keys\n\n    @argument authors\n    @type {Array<Object>}\n  */\n\n  /**\n    Sponsor object with `name`, `url`, and `image` keys\n\n    @argument sponsor\n    @type {Object}\n  */\n\n});",
  "nypr-m-figcaption-caption.hbs": "{{#if (or (has-block) @caption)}}\n  <span class=\"o-icon o-icon--dashed-arrow u-icon--s\">\n    <NyprASvg @icon='arrow-dashed'/>\n  </span>\n{{/if}}\n<div class=\"o-caption__text\">\n  {{#if (has-block)}}\n    <span>\n      {{yield}}\n    </span>\n  {{else if @caption}}\n    <span>\n      {{{@caption}}}\n    </span>\n  {{/if}}\n\n  {{#if @credit}}\n    {{nypr-m-figcaption/credit\n      creditLink=@creditLink\n      credit=@credit\n      includeIcon=(and (not @caption) (not (has-block)))}}\n  {{/if}}\n</div>",
  "nypr-m-figcaption-caption.js": "import Component from '@ember/component';\nimport layout from '../../templates/components/nypr-m-figcaption/caption';\n\nexport default Component.extend({\n  layout,\n  tagName: '',\n});",
  "nypr-m-figcaption-credit.hbs": "<span class=\"o-credit\">\n  {{#if @includeIcon}}\n    <span class=\"o-icon o-icon--dashed-arrow u-icon--s\">\n      <NyprASvg @icon='arrow-dashed'/>\n    </span>\n    <span>\n      {{#if (has-block)}}\n        {{yield}}\n      {{else}}\n        {{#if @creditLink}}\n          <a href={{@creditLink}} rel=\"noopener\" target=\"_blank\">\n            {{{@credit}}}\n          </a>\n        {{else}}\n          {{{@credit}}}\n        {{/if}}\n      {{/if}}\n    </span>\n  {{else}}\n    {{#if (has-block)}}\n      {{yield}}\n    {{else}}\n      {{#if @creditLink}}\n        <a href={{@creditLink}} rel=\"noopener\" target=\"_blank\">\n          {{{@credit}}}\n        </a>\n      {{else}}\n        {{{@credit}}}\n      {{/if}}\n    {{/if}}\n  {{/if}}\n</span>",
  "nypr-m-figcaption-credit.js": "import Component from '@ember/component';\nimport layout from '../../templates/components/nypr-m-figcaption/credit';\n\nexport default Component.extend({\n  layout,\n  tagName: '',\n});",
  "nypr-m-figcaption.hbs": "{{#if (has-block)}}\n  {{yield (hash\n    caption=(component 'nypr-m-figcaption/caption')\n    credit=(component 'nypr-m-figcaption/credit')\n  )}}\n{{else}}\n  {{#if (or @caption @credit)}}\n    {{nypr-m-figcaption/caption caption=@caption credit=@credit creditLink=@creditLink}}\n  {{/if}}\n{{/if}}",
  "nypr-m-figcaption.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-m-figcaption';\n\n/**\n  `<figcaption/>` molecule for a `<figure/>` element.\n\n  Usage:\n  ```hbs\n  <NyprMFigcaption @caption=\"hello world\" @credit=\"Getty Images\" />\n  ```\n  @class nypr-m-figcaption\n  @yield {Hash} hash\n  @yield {Component} hash.caption `nypr-m-figcaption/caption`\n  @yield {Component} hash.credit `nypr-m-figcaption/credit`\n*/\nexport default Component.extend({\n  layout,\n\n  tagName: 'figcaption',\n  classNames: ['o-caption'],\n\n  /**\n    @argument caption\n    @type {String}\n  */\n\n  /**\n    @argument credit\n    @type {String}\n  */\n});",
  "nypr-m-figure-image.hbs": "<NyprAPicture\n  @breakXl={{@breakXl}}\n  @srcXl={{@srcXl}}\n  @breakL={{@breakL}}\n  @srcL={{@srcL}}\n  @breakM={{@breakM}}\n  @srcM={{@srcM}}\n  @breakS={{@breakS}}\n  @srcS={{@srcS}}\n  @lazy={{@lazy-load}}\n  @alt={{or @alt @caption}}\n  @ratio={{@ratio}}\n  @width={{@width}}\n  @height={{@height}}\n/>",
  "nypr-m-figure-image.js": "import Component from '@ember/component';\nimport layout from '../../templates/components/nypr-m-figure/image';\n\nexport default Component.extend({\n  layout,\n  classNames: ['o-figure__image'],\n});",
  "nypr-m-figure.hbs": "{{#if (has-block)}}\n  {{yield (hash\n    image=(component 'nypr-m-figure/image')\n    figcaption=(component 'nypr-m-figcaption')\n  )}}\n{{else}}\n  {{nypr-m-figure/image\n    breakXl=@breakXl\n    srcXl=@srcXl\n    breakL=@breakL\n    srcL=@srcL\n    breakM=@breakM\n    srcM=@srcM\n    breakS=@breakS\n    srcS=@srcS\n    lazy=@lazy-load\n    alt=(or @alt @caption)\n    ratio=@ratio\n    width=@width\n    height=@height\n  }}\n\n  <NyprMFigcaption @caption={{@caption}} @credit={{@credit}} @creditLink={{@creditLink}} />\n{{/if}}",
  "nypr-m-figure.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-m-figure';\nimport { computed } from '@ember/object';\n\n/**\n  A `<figure/>` element with an optional caption.\n\n  See [nypr-a-picture](/docs/atoms/nypr-a-picture) and [nypr-m-figcaption](/docs/atoms/nypr-m-figcaption) for additional arguments.\n\n  Usage:\n  ```hbs\n  <NyprMFigure @caption='hello' @credit='getty' @srcS='https://picsum.photos/800/600' />\n  ```\n\n  @class nypr-m-figure\n  @yield {Block} block Arbitrary body for the component.\n  @yield {Hash} hash\n  @yield {Component} hash.image `nypr-m-figure/image`\n  @yield {Component} hash.figcaption `nypr-m-figcaption`\n*/\nexport default Component.extend({\n  tagName: 'figure',\n  classNames: ['o-figure'],\n  classNameBindings: ['captionAlignment'],\n  attributeBindings: ['role'],\n\n  layout,\n\n  role: 'group',\n\n  /**\n    Specify a horizontal alignment.\n    Accepts `'l'`, `'c'`, `'r'` for left, center, and right\n\n    @argument align-caption\n    @type {String?}\n  */\n  'align-caption': null,\n\n  /**\n    @accessor captionAlignment\n    @type {String}\n  */\n  captionAlignment: computed('align-caption', function() {\n    switch(this['align-caption']) {\n      case 'l':\n        return 'u-alignleft';\n      case 'c':\n        return 'u-aligncenter';\n      case 'r':\n        return 'u-alignright';\n    }\n  }),\n});",
  "nypr-m-gallery-lead.hbs": "<div class=\"c-lead-gallery__top\">\n\n  <NyprMFigure\n    data-test-gallery-lead-preview\n    as |figure|>\n    <figure.image\n      @srcS={{this.currentSlide.srcS}}\n      @alt={{this.currentSlide.alt}}\n      onclick={{action 'goToSlide'}}\n      data-test-gallery-current\n    />\n\n    <figure.figcaption as |caption|>\n      {{#if this.currentSlide.credit}}\n        <caption.credit @credit={{this.currentSlide.credit}} @creditLink={{this.currentSlide.creditLink}} @includeIcon={{unless this.currentSlide.caption true}}/>\n      {{/if}}\n\n      {{#if this.currentSlide.title}}\n        <h2 class=\"c-lead-gallery__title\">{{this.currentSlide.title}}</h2>\n      {{/if}}\n\n      {{#if this.currentSlide.caption}}\n        <caption.caption @caption={{this.currentSlide.caption}} />\n      {{/if}}\n    </figure.figcaption>\n  </NyprMFigure>\n\n</div>\n\n<div class=\"c-lead-gallery__thumbs\">\n  {{#each @slides as |slide i|}}\n    {{#if (lt i 4)}}\n      <button {{action 'setCurrent' i}} class=\"c-lead-gallery__thumbs-thumb {{if (eq this.currentIndex i) 'is-active'}}\" data-test-gallery-thumb={{i}}>\n        <img\n          src={{slide.thumb}}\n          srcset={{slide.thumbSrcSet}}\n          alt={{slide.alt}}\n        >\n      </button>\n    {{/if}}\n  {{/each}}\n  <button\n    data-test-gallery-lead-view-all\n    class=\"c-lead-gallery__thumbs-thumb c-lead-gallery__thumbs-thumb-text u-font--secondary-style u-font--xs\"\n    {{action (optional @viewAll)}}\n  >\n    View all\n    <span>{{@slides.length}}</span>\n  </button>\n</div>",
  "nypr-m-gallery-lead.js": "import Component from '@ember/component';\nimport { computed, get } from '@ember/object';\n\nimport layout from '../templates/components/nypr-m-gallery-lead';\n\n/**\n  Gallery Lead\n  Preview galleries with 3 thumbnails and an optional viewAll action\n\n  @class nypr-m-gallery-lead\n*/\nexport default Component.extend({\n  layout,\n  classNames: ['c-lead', 'c-lead-gallery'],\n\n  currentIndex: 0,\n\n  currentSlide: computed('currentIndex', function() {\n    if (!get(this, 'slides.length')) {\n      return;\n    } else {\n      return this.slides[this.currentIndex];\n    }\n  }),\n\n  actions: {\n    setCurrent(index) {\n      this.set('currentIndex', index);\n    },\n    goToSlide() {\n      if (this.goToSlide) {\n        this.goToSlide(this.currentIndex);\n      }\n    }\n  },\n\n  /**\n    Handler for \"View All\" button\n\n    @argument viewAll\n    @type {Function}\n  */\n\n  /**\n    Handler for clicks on the current slide button\n\n    @argument goToSlide\n    @type {Function}\n  */\n});",
  "nypr-m-inline-search-form.js": "import ClickOutsideMixin from 'ember-click-outside/mixin';\nimport { closest } from 'ember-click-outside/utils';\n\nimport Component from '@ember/component';\nimport { bind } from '@ember/runloop';\nimport { computed } from '@ember/object';\n\nimport layout from '../../templates/components/nypr-m-inline-search/form';\n\nexport default Component.extend(ClickOutsideMixin, {\n  layout,\n  tagName: 'form',\n  classNames: ['c-search'],\n  classNameBindings: ['isOpen'],\n  attributeBindings: ['role', 'ariaHidden:aria-hidden'],\n  role: 'dialog',\n\n  ariaHidden: computed('isOpen', function() {\n    return this.get('isOpen') ? \"false\" : \"true\";\n  }),\n\n  init() {\n    this._super(...arguments);\n\n    // shadow passed in value\n    // but don't bind to it\n    if (this.query) {\n      this.set('value', this.query);\n    }\n  },\n\n  didUpdateAttrs() {\n    this._super(...arguments);\n\n    // update local value if passed in query is updated\n    this.set('value', this.query);\n  },\n\n  didInsertElement() {\n    this._super(...arguments);\n\n    this.addClickOutsideListener();\n\n    this._boundTransitionHandler = bind(this, 'focusInput');\n\n    this.element.addEventListener('transitionend', bind(this, 'focusInput'));\n  },\n\n  willDestroyElement() {\n    this.removeClickOutsideListener();\n    this.element.removeEventListener('transitionend', this._boundTransitionHandler);\n  },\n\n  focusInput(e) {\n    let input = this.element.querySelector('.c-search__input');\n    if (e.target === input && this.isOpen) {\n      input.focus();\n    }\n  },\n\n  submit(e) {\n    e.preventDefault();\n    if (this.value) {\n      this.search(this.value);\n\n      if (this.isOpen) {\n        // if it's block form, clear the input value\n        this.set('value', null);\n      }\n    } else {\n      this.doClose();\n    }\n  },\n\n\n  /**\n    Handler called by `ClickOutsideMixin`\n\n    @method clickOutside\n    @param {Event} e\n    @return {void}\n  */\n  clickOutside(e) {\n    if (!closest(e.target, '.c-search') && !closest(e.target, '.c-search-toggle')) {\n      this.doClose();\n    }\n  },\n\n  /**\n    Handles closing the form. Clears the input and guards for if the action isn't passed in\n\n    @method doClose\n    @return {void}\n  */\n  doClose() {\n    if (this.close) {\n      this.set('value', null); // clear the input value on close\n      this.close();\n    }\n  },\n\n  /**\n    Close button handler\n\n    @argument close\n    @type {Function}\n  */\n\n  /**\n    Perform a search with given input value\n\n    @argument search\n    @type {Function}\n  */\n\n  /**\n    Is the form open or closed. Only set in block form.\n\n    @field isOpen\n    @type {Boolean}\n  */\n\n});",
  "nypr-m-inline-search.hbs": "{{#if (has-block)}}\n  {{yield (hash\n    open=(component 'nypr-m-inline-search/open'\n      onclick=(action (mut this.isOpen) true)\n      ariaExpanded=(if this.isOpen 'true' 'false')\n    )\n    form=(component 'nypr-m-inline-search/form'\n      close=(action (mut this.isOpen) false)\n      class=@formClass\n      isOpen=this.isOpen\n    )\n  )}}\n{{else}}\n  {{nypr-m-inline-search/form\n    search=(action (optional @search))\n    class=@formClass\n    query=@query\n  }}\n{{/if}}",
  "nypr-m-inline-search.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-m-inline-search';\n\n/**\n  Inline search form with optional expandable input\n\n  Usage:\n  ```hbs\n  {{!-- block usage --}}\n  <NyprMInlineSearch as |search|>\n    <search.open/>\n    <search.form @search={{action 'search'}}/>\n  </NyprMInlineSearch>\n\n  {{!-- inline usage --}}\n  <NyprMInlineSearch @search={{action 'search'}} />\n  ```\n\n  @class nypr-m-inline-search\n  @yield {Hash} hash\n  @yield {Component} hash.open `nypr-m-inline-search/open`\n  @yield {Component} hash.form `nypr-m-inline-search/form`\n*/\nexport default Component.extend({\n  layout,\n  tagName: '',\n\n  /**\n    Class list passed to `nypr-m-inline-search/form` component\n\n    @argument formClass\n    @type {String}\n  */\n\n  /**\n    Perform a search with given query. Passed to nested `nypr-m-inline-search/form` component.\n\n    @argument search\n    @type {Function}\n  */\n\n  /**\n    Controls whether or not the input is exapanded in block contexts\n\n    @field isOpen\n    @type {Boolean}\n  */\n  isOpen: false,\n});",
  "nypr-m-newsletter.hbs": "<div class=\"c-newsletter-form__graphic\">\n  {{yield (hash graphic=(component 'blank-template'))}}\n</div>\n\n<div class=\"c-newsletter-form__inner u-spacing\">\n\n  <div class=\"c-newsletter-form__text\">\n    <p>\n      <span class=\"c-newsletter-form__icon o-icon o-icon--newsletter-form u-icon--s\">\n        <NyprASvg @icon='arrow-dashed'/>\n      </span>\n      <span data-test-newsletter-blurb>\n        {{yield (hash blurb=(component 'blank-template'))}}\n      </span>\n    </p>\n  </div>\n\n  <NyprMNewsletterForm class=\"u-spacing\" @endpoint={{@endpoint}} @params={{@params}} as |form|>\n    <form.label/>\n\n    <form.input/>\n\n    <form.submit class=\"o-button--c-quarternary\">\n      <span class=\"c-newsletter-form__submit-icon o-icon o-icon--newsletter-form\">\n        <NyprASvg @icon='arrow-dashed'/>\n      </span>\n      <span class=\"is-vishidden\">Submit email</span>\n    </form.submit>\n\n    <form.legal>\n      {{yield (hash legal=(component form.legal))}}\n    </form.legal>\n  </NyprMNewsletterForm>\n</div>",
  "nypr-m-newsletter.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-m-newsletter';\n\n/**\n  Newsletter sign up widget\n\n  @class nypr-m-newsletter\n  @yield {Hash} hash\n  @yield {Block} hash.graphic `blank-template`\n  @yield {Block} hash.blurb `blank-template`\n  @yield {Block} hash.legal `blank-template`\n*/\nexport default Component.extend({\n  layout,\n  classNames: ['c-newsletter-form'],\n});",
  "nypr-m-progress-bar.hbs": "<progress class=\"o-progress\" value={{this.progress}} />",
  "nypr-m-progress-bar.js": "import Component from '@ember/component';\nimport { debounce, bind } from '@ember/runloop';\nimport layout from '../templates/components/nypr-m-progress-bar';\n\n\nconst DEBOUNCE_TIMER = 75;\n\n/**\n  Progress bar to display how much of `document.body` has been scrolled.\n\n  Usage:\n  ```hbs\n  <NyprMProgressBar/>\n  ```\n\n  @class nypr-m-progress-bar\n*/\nexport default Component.extend({\n  layout,\n  tagName: '',\n\n  /**\n    Percentage of body scrolled\n\n    @field progress\n    @type {Number}\n  */\n  progress: 0,\n\n  didInsertElement() {\n    this._super(...arguments);\n    this._boundListener = bind(this, '_measure');\n    window.addEventListener('scroll', this._boundListener);\n    window.addEventListener('resize', this._boundListener);\n\n    this._measure();\n  },\n\n  willDestroyElement() {\n    this._super(...arguments);\n    window.removeEventListener('scroll', this._boundListener);\n    window.removeEventListener('resize', this._boundListener);\n  },\n\n  /**\n    Measure percentage of current body has been scrolled\n\n    @method _measure\n    @return {void}\n  */\n  _measure() {\n    debounce(this, () => {\n      if (this.isDestroyed || this.isDestroying) {\n        return;\n      }\n      let target = document.querySelector(`${this.target}`);\n      if (!target) {\n        target = document.body;\n      }\n      let distanceToTargetBottom = target.offsetHeight + target.offsetTop;\n      let parent = target.offsetParent;\n      while(parent) {\n        distanceToTargetBottom += parent.offsetTop;\n        parent = parent.offsetParent;\n      }\n\n      let scrolled = window.pageYOffset;\n      let windowHeight = window.innerHeight;\n      let progress = scrolled / (distanceToTargetBottom - windowHeight);\n      this.set('progress', isNaN(progress) ? 0 : progress);\n    }, DEBOUNCE_TIMER);\n  }\n});",
  "nypr-m-pullquote-attribution.hbs": "{{#if (has-block)}}\n  {{yield}}\n{{else}}\n  {{@attribution}}\n{{/if}}",
  "nypr-m-pullquote-attribution.js": "import Component from '@ember/component';\nimport layout from '../../templates/components/nypr-m-pullquote/attribution';\n\nexport default Component.extend({\n  layout,\n\n  tagName: 'span',\n  classNames: ['o-pullquote__author'],\n});",
  "nypr-m-pullquote-quote.hbs": "<span class=\"o-bg-text-accent\">\n  {{#if (has-block)}}\n    {{yield}}\n  {{else}}\n    {{@quote}}\n  {{/if}}\n</span>",
  "nypr-m-pullquote-quote.js": "import Component from '@ember/component';\nimport layout from '../../templates/components/nypr-m-pullquote/quote';\n\nexport default Component.extend({\n  layout,\n\n  tagName: 'p',\n});",
  "nypr-m-pullquote.hbs": "{{#if (has-block)}}\n  {{yield (hash\n    quote=(component 'nypr-m-pullquote/quote')\n    attribution=(component 'nypr-m-pullquote/attribution')\n  )}}\n{{else}}\n  {{nypr-m-pullquote/quote quote=@quote}}\n  {{nypr-m-pullquote/attribution attribution=@attribution}}\n{{/if}}",
  "nypr-m-pullquote.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-m-pullquote';\n\nexport default Component.extend({\n  layout,\n\n  tagName: 'blockquote',\n  classNames: ['o-pullquote']\n});",
  "nypr-m-secondary-nav.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-m-secondary-nav';\n\n/**\n  @class nypr-m-secondary-nav\n  @deprecated\n*/\nexport default Component.extend({\n  layout,\n  tagName: 'nav',\n  classNames: ['c-secondary-nav'],\n\n  attributeBindings: ['aria-label'],\n  'aria-label': 'Secondary',\n\n  /**\n    Navigation items objects with `url` and `title` keys.\n\n    @argument navItems\n    @type {Array<Object>}\n  */\n});",
  "nypr-m-share-tools-link.hbs": "<a href={{or @href this.href}} class=\"c-share-tools__link o-icon {{@service}}\" target=\"_blank\" rel=\"noopener\">\n  <NyprASvg @icon={{@service}}/>\n</a>",
  "nypr-m-share-tools-link.js": "import Component from '@ember/component';\nimport layout from '../../templates/components/nypr-m-share-tools/link';\nimport { computed } from '@ember/object';\n\nimport { SERVICE_MAP } from '../nypr-m-share-tools';\n\nexport default Component.extend({\n  layout,\n  tagName: '',\n\n  /**\n    Social network service name to link to.\n\n    @argument service\n    @type {String}\n  */\n\n  /**\n    Username on social network service\n\n    @argument username\n    @type {String}\n  */\n\n  /**\n    URL of social profile. Can be passed in as an argument, or else will be\n    dervied based on the given username and service\n\n    @argument href\n    @type {String}\n  */\n  href: computed('service', 'username', function() {\n    let { username, service } = this;\n    if (!SERVICE_MAP[service]) {\n      return;\n    }\n\n    return `${SERVICE_MAP[service].profileBase}${username}`;\n  }),\n});",
  "nypr-m-share-tools-share.hbs": "<button class=\"c-share-tools__link o-icon\" {{action 'openShare'}} aria-label={{@ariaLabel}} ...attributes>\n  {{#if (has-block)}}\n    {{yield}}\n  {{else}}\n    <NyprASvg @icon={{@service}}/>\n  {{/if}}\n</button>",
  "nypr-m-share-tools-share.js": "import Component from '@ember/component';\nimport layout from '../../templates/components/nypr-m-share-tools/share';\n\nimport { SERVICE_MAP } from '../nypr-m-share-tools';\n\nconst windowString = ({top, left}) =>\n  `location=no,toolbar=no,menubar=no,scrollbars=no,status=no,width=550,height=600,top=${top},left=${left}`\n\n/**\n  Share Tools Share Button\n\n  @class nypr-m-share-tools/share\n*/\nexport default Component.extend({\n  tagName: '',\n  layout,\n\n  /**\n    Target service of share dialog\n\n    @argument service\n    @type {String}\n  */\n\n  /**\n    Hash of key value pairs to pass to service via query string\n\n    @argument params\n    @type {Object}\n  */\n\n  /**\n    Optional URL to override the derived share URL\n\n    @argument url\n    @type {String}\n  */\n\n  /**\n    Measure window center to position pop up\n\n    @method getPopupPosition\n    @return {Object} positions\n  */\n  getPopupPosition() {\n    const dualScreenLeft = screen.availLeft;\n    const dualScreenTop = screen.availTop;\n\n    const windowWidth = screen.availWidth;\n    const windowheight =  screen.availHeight;\n\n    const left = ((windowWidth / 2) - (600 / 2)) + dualScreenLeft;\n    const top = ((windowheight / 2) - (600 / 2)) + dualScreenTop;\n\n    return {left: left, top: top};\n  },\n\n  /**\n    Build a URL to the given services share dialog\n\n    @method getURL\n    @param {String} override\n    @return {String} url\n  */\n  getURL(override) {\n    if (!SERVICE_MAP[this.service]) {\n      return;\n    }\n    let { shareBase, getParams } = SERVICE_MAP[this.service];\n    let url = override || window.location.toString();\n\n    if (this.utm) {\n      let utmParams = Object.keys(this.utm).map(key => `utm_${key}=${this.utm[key]}`).join('&');\n\n      if (url.indexOf('?') > -1) {\n        url += `&${utmParams}`;\n      } else {\n        url += `?${utmParams}`;\n      }\n      url = encodeURIComponent(url);\n    }\n\n    return `${shareBase}?${getParams(url, this.params)}`\n  },\n\n\n  /**\n    Opens a share dialog\n\n    @method openShare\n    @return {void}\n  */\n  actions: {\n    openShare() {\n      if (!this.service) {\n        return;\n      }\n\n      let urlOverride = this.url;\n      let url = this.getURL(urlOverride);\n      let popupPosition = this.getPopupPosition();\n      var newWindow = window.open(url, 'share window', windowString(popupPosition));\n\n      // make sure it actually opened and bring it to the front\n      if (typeof newWindow !== 'undefined' && newWindow !== null && newWindow.focus) {\n        newWindow.focus();\n      }\n    }\n  }\n\n});",
  "nypr-m-share-tools.hbs": "{{#if @label}}\n  <span class=\"c-share-tools__label u-font--secondary-style u-font--xs u-color--white\">{{@label}}</span>\n{{/if}}\n<div class=\"c-share-tools__group\">\n  {{yield (hash\n    share=(component 'nypr-m-share-tools/share')\n    link=(component 'nypr-m-share-tools/link')\n  )}}\n</div>",
  "nypr-m-share-tools.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-m-share-tools';\n\nexport default Component.extend({\n  layout,\n  classNames: ['c-share-tools'],\n  classNameBindings: ['vertical:c-share-tools--vertical'],\n\n  /**\n    Use vertical orientation\n\n    @argument vertical\n    @type {Boolean}\n  */\n  vertical: false,\n\n  /**\n    Label text\n\n    @argument label\n    @type {String}\n  */\n});",
  "nypr-m-tags.hbs": "{{#if (has-block)}}\n  {{#each @tags as |tag|}}\n    {{yield (component 'nypr-a-tag' tag=tag prefix=(or @prefix this.prefix)) tag}}\n  {{/each}}\n{{else}}\n  {{#each @tags as |tag|}}\n    <NyprATag @tag={{tag}} @prefix={{or @prefix this.prefix}} />\n  {{/each}}\n{{/if}}",
  "nypr-m-tags.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-m-tags';\n\n/**\n  Display a list of tags.\n\n  Usage:\n  ```hbs\n  <NyprMTags @tags={{tags}} as |Tag tag|>\n    <Tag @url='/tags/{{tag}}' />\n  </NyprMTags>\n  ```\n\n  @class nypr-m-tags\n  @yield {Component} nypr-a-tag\n  @yield {String} tag\n*/\nexport default Component.extend({\n  layout,\n  classNames: ['o-tags'],\n\n  /**\n    List of tag values to render.\n\n    @argument tags\n    @type {Array<String>}\n  */\n  tags: null,\n\n  /**\n    Prefix the rendered tag with a value. Defaults to `\"#\"`.\n    Set to `''` to blank out.\n\n    @argument prefix\n    @type {String}\n  */\n  prefix: '#',\n});",
  "nypr-m-text-banner.hbs": "<NyprMBlock @urgent={{true}} as |block| >\n  <block.object as |o|>\n    <o.eyebrow @text={{or @category 'Breaking News'}} />\n    <o.title @h2 @url={{@url}} @route={{@route}}>\n      {{@title}}\n    </o.title>\n    <o.body as |body|>\n      <body.meta @timestamp={{@timestamp}}/>\n      <body.text>\n        {{{@description}}}\n      </body.text>\n    </o.body>\n  </block.object>\n</NyprMBlock>",
  "nypr-m-text-banner.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-m-text-banner';\n\n/**\n  A banner to display story details for Breaking News.\n\n  Usage:\n  ```hbs\n    <NyprMTextBanner\n      @category='Breaking News'\n      @url='Link'\n      @route={{array 'article' 'slug'}}\n      @title='Story Title'\n      @description='Short summary of the story'\n    />\n  ```\n\n  @class o-text-banner\n  @class u-spacing--half\n*/\nexport default Component.extend({\n  layout,\n  classNames: ['o-text-banner', 'u-spacing--half'],\n\n  /**\n    Category\n\n    @argument category\n    @type {String}\n  */\n\n  /**\n    URL\n    Link to an external page\n    Use this or @route for a link\n\n    @argument url\n    @type {String}\n  */\n\n  /**\n    Route\n    Array of Routing Parameters\n    Use this or @url for a link\n\n    @argument route\n    @type {[String]}\n  */\n\n  /**\n    Title\n\n    @argument title\n    @type {String}\n  */\n\n  /**\n    Description\n\n    @argument description\n    @type {String}\n  */\n});",
  "nypr-m-to-top.hbs": "<button {{action toTop}} class=\"o-to-top u-font--secondary-style u-font--xs u-color--black\" data-test-to-top>\n  <span class=\"o-icon o-icon--up-arrow u-icon--m\">\n    <NyprASvg @icon='arrow-dashed'/>\n  </span>\n  <strong>Back to Top</strong>\n</button>",
  "nypr-m-to-top.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-m-to-top';\n\nexport default Component.extend({\n  layout,\n  tagName: '',\n\n  /**\n    Optional dom selector scroll target\n\n    @argument selector\n    @type {String}\n  */\n\n  /**\n    Optional offset from selector target\n\n    @argument offset\n    @type {Number}\n  */\n  offset: 0,\n\n  /**\n    Scrolls window to top of page or provided selector\n\n    @method toTop\n    @return {Void}\n  */\n  toTop() {\n    let offset = this.offset;\n    let target = document.querySelector(this.selector);\n    let y = target ? target.offsetTop - (target.offsetHeight + offset) : 0;\n    window.scrollTo({top: y, behavior: 'smooth'});\n\n    this.focusOnTop();\n  },\n\n  /**\n    Focus screenreaders at top of page too\n\n    @method focusOnTop\n    @return {Void}\n  */\n  focusOnTop(){\n    let header = document.getElementsByTagName(\"header\")[0];\n    if (header){\n      header.setAttribute('tabIndex', -1);\n      header.focus();\n    }\n\n  }\n});",
  "nypr-o-article-body.hbs": "{{#if (has-block)}}\n  {{yield}}\n{{else}}\n  {{{@body}}}\n{{/if}}",
  "nypr-o-article-body.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-o-article-body';\nimport { schedule } from '@ember/runloop';\n\n/**\n Article body element\n\n Usage:\n ```hbs\n <NyprOArticleBody>\n  <p>some <strong>raw</strong> HTML</p>\n</NyprOArticleBody>\n```\n\n @class nypr-o-article-body\n @yield {Block} Element contents\n*/\nexport default Component.extend({\n  layout,\n  classNames: ['c-article__body', 'o-rte-text', 'u-spacing', 'l-container--content'],\n\n  /**\n    Body content.\n    **Warning** will be rendered raw, unescaped into the DOM\n\n    @argument body\n    @type {String}\n  */\n  body: null,\n\n  didRender() {\n    schedule('afterRender', () => {\n      // instagram embeds need a manual push after rehydration\n      // ember will re-render a fastboot DOM tree,\n      // but the IG embed script will only operate once\n      if (typeof instgrm !== 'undefined') {\n        instgrm.Embeds.process();\n      }\n\n      // load twitter widgets manually after twitter callback has run\n      // twitter scripts are stripped from gothamist payload responses to allow\n      // for more reliable rendering\n      if (typeof twttr !== 'undefined') {\n        twttr.ready(() => twttr.widgets.load());\n      }\n\n      // parse any facebook embeds\n      if (typeof FB !== 'undefined') {\n        FB.XFBML.parse();\n      }\n    });\n  }\n});",
  "nypr-o-article-footer.hbs": "<div class=\"c-article__tags\" data-test-article-footer-tags>\n  {{yield (hash tags=(component 'nypr-m-tags'))}}\n</div>\n\n<div class=\"u-section-spacing\">\n  <div class=\"c-article__contact\" data-test-article-footer-contact>\n    {{yield (hash contact=(component 'nypr-o-contact'))}}\n  </div>\n\n  <div class=\"c-article__donate\" data-test-article-footer-donate>\n    {{yield (hash donate=(component 'nypr-o-donate' isActive=this.inViewport))}}\n  </div>\n\n  <div class=\"c-article__ad\" data-test-article-footer-ad>\n    {{yield (hash ad=(component 'blank-template'))}}\n  </div>\n\n  <div class=\"c-article__comments\" data-test-article-footer-comments>\n    {{yield (hash comments=(component 'blank-template'))}}\n  </div>\n</div>",
  "nypr-o-article-footer.js": "import Component from '@ember/component';\nimport { debounce, bind } from '@ember/runloop';\n\nimport layout from '../templates/components/nypr-o-article-footer';\n\n/**\n  Article Footer layout\n\n  Usage:\n  ```hbs\n  <NyprOArticleFooter as |footer|>\n    <footer.tags @tags={{array 'foo' 'bar'}} as |Tag tag|>\n      <Tag @url='http://example.com/tags/{{tag}}'/>\n    </footer.tags>\n\n    <footer.contact>\n      Contact us because you've got something to say\n    </footer.contact>\n\n    <footer.donate @linkText='Click' @linkUrl='https://example.com/pledge' @message='Donate because you like us'/>\n\n    <footer.comments>\n      comments go here\n    </footer.comments>\n  </NyprOArticleFooter>\n  ```\n\n  @class nypr-o-article-footer\n  @yield {Hash} hash\n  @yield {Component} hash.tags `nypr-m-tags`\n  @yield {Component} hash.contact `nypr-o-contact`\n  @yield {Component} hash.donate `nypr-o-donate`\n  @yield {Block} hash.comments `blank-template`\n*/\n\nexport default Component.extend({\n  layout,\n  classNames: ['c-article__footer', 'u-section-spacing'],\n\n  didInsertElement() {\n    this._super(...arguments);\n    this._boundListener = bind(this, '_watchForTout');\n    window.addEventListener('scroll', this._boundListener);\n  },\n\n  willDestroyElement() {\n    this._super(...arguments);\n    window.removeEventListener('scroll', this._boundListener);\n  },\n\n  /**\n    Indicates whether or not this element is in the viewport\n\n    @field inViewport\n    @type {Boolean}\n  */\n  inViewport: false,\n\n  /**\n    Measures whether the footer is scrolled into view, and shows donate tout if so.\n\n    @method _watchForTout\n    @param {EventObject} event\n    @return {void}\n  */\n  _watchForTout(/* e */) {\n    debounce(this, () => {\n      if (this.isDestroyed || this.isDestroying) {\n        return; // JIC\n      }\n      let toutInDom = this.element.querySelector('.c-donate-tout');\n      let windowCenter = document.documentElement.clientHeight / 2;\n      let footerTopEdge = this.element.getBoundingClientRect().top;\n\n      if (toutInDom && footerTopEdge - windowCenter < 0) {\n        this.set('inViewport', true);\n        window.removeEventListener('scroll', this._watchForTout);\n      }\n    }, 150);\n  }\n});",
  "nypr-o-article-header-lead.hbs": "<div class=\"l-container l-container--10col\">\n  <div class=\"c-article-header__left c-article__share u-border-accent u-border-accent--right\">\n    {{yield (hash left=(component 'blank-template'))}}\n  </div>\n\n  <div class=\"c-article-header__center\">\n    {{yield (hash center=(component 'blank-template'))}}\n  </div>\n</div>\n\n<div class=\"c-article__related c-article__related--wide-screens u-border-accent u-border-accent--left\">\n  {{yield (hash right=(component 'blank-template'))}}\n</div>",
  "nypr-o-article-header-meta.hbs": "<div class=\"c-article__meta u-spacing--half u-font--secondary-style u-font--xxs\">\n  {{yield (hash\n    byline=(component 'nypr-m-byline')\n  )}}\n  <div class=\"c-article__meta-group\">\n    {{yield (hash publishDate=(component 'nypr-o-article-header/publish-date'))}}\n\n    {{#if @commentCount}}\n      &bull;\n      <a\n        href={{@commentsUrl}}\n        class=\"u-has-accent u-has-accent--secondary\"\n        data-test-comments-link\n      >\n        {{@commentCount}} {{if (eq @commentCount 1) 'Comment' 'Comments'}}\n      </a>\n    {{/if}}\n\n    {{#if @galleryPhotosCount}}\n      &bull;\n      {{#link-to params=@galleryRoute class=\"u-has-accent u-has-accent--secondary\" data-test-gallery-link=true}}\n        {{@galleryPhotosCount}} {{if (eq @galleryPhotosCount 1) 'Photo' 'Photos'}}\n        <span class=\"o-icon o-icon--gallery u-icon--xs\">\n          <NyprASvg @icon='gallery'/>\n        </span>\n      {{/link-to}}\n    {{/if}}\n  </div>\n</div>",
  "nypr-o-article-header-publish-date.hbs": "<span class=\"o-published-date__time\">\n  <time datetime={{moment-format @publishDate 'YYYY-MM-DDThh:mmZ' @inputFormat}}>\n    {{moment-format @publishDate (or @format this.format) @inputFormat}}\n  </time>\n</span>",
  "nypr-o-article-header-publish-date.js": "import Component from '@ember/component';\nimport layout from '../../templates/components/nypr-o-article-header/publish-date';\n\nexport default Component.extend({\n  layout,\n  classNames: ['o-published-date'],\n\n  /**\n    Publish datetime\n\n    @argument publishDate\n    @type {String}\n  */\n\n  /**\n    Display formatting for `publishDate`, according to moment.js rules: https://momentjs.com/docs/#/displaying/format/\n    Defaults to `'MMM D, YYYY h:mmA'`\n\n    @argument format\n    @type {String}\n  */\n  format: 'MMM D, YYYY h:mm A'\n});",
  "nypr-o-article-header-top.hbs": "<div class=\"l-container l-container--12col u-spacing--and-half\">\n  {{yield (hash\n    breadcrumb=(component 'nypr-m-breadcrumb')\n    headline=(component 'nypr-a-story-headline' class='c-article__headline')\n  )}}\n</div>\n\n{{yield (hash\n  meta=(component 'nypr-o-article-header/meta')\n)}}",
  "nypr-o-article-header.hbs": "{{yield (hash\n  top=(component 'nypr-o-article-header/top')\n  lead=(component 'nypr-o-article-header/lead')\n)}}",
  "nypr-o-article-header.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-o-article-header';\n\n/**\n  Header layout for articles. See [nypr-o-article-header](/docs/organisms/nypr-o-article-header) for usage.\n\n  @class nypr-o-article-header\n  @yield {Hash} hash\n  @yield {Hash} hash.top `nypr-o-article-header/top`\n  @yield {Component} hash.top.breadcrumb `nypr-m-breadcrumb`\n  @yield {Component} hash.top.headline `nypr-a-story-headline`\n  @yield {Hash} hash.top.meta `nypr-o-article-header/meta`\n  @yield {Component} hash.top.meta.byline `nypr-m-byline`\n  @yield {Component} hash.top.meta.publishDate `nypr-o-article-header/publish-date`\n  @yield {Component} hash.lead `nypr-o-article-header/lead`\n  @yield {Block} hash.lead.left `blank-template`\n  @yield {Block} hash.lead.center `blank-template`\n  @yield {Block} hash.lead.right `blank-template`\n*/\nexport default Component.extend({\n  layout,\n  tagName: 'header',\n  classNames: ['c-article__header', 'u-spacing--and-half', 'l-container'],\n});",
  "nypr-o-contact.hbs": "{{#if (has-block)}}\n  {{yield}}\n{{else}}\n  {{@text}}\n{{/if}}",
  "nypr-o-contact.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-o-contact';\n\n/**\n  A contact tout\n\n  Usage:\n  ```hbs\n  <NyprOContact>\n    <p><strong>hello</strong> <em>world</em></p>\n  </NyprOContact>\n  ```\n\n  @class nypr-o-contact\n  @yield {Block} element contents\n*/\nexport default Component.extend({\n  layout,\n  classNames: ['c-contact-tout', 'o-rte-text', 'u-reversed-out', 'o-3d-heading'],\n\n  /**\n    Text content\n\n    @argument text\n    @type {String}\n  */\n  text: null,\n});",
  "nypr-o-donate.hbs": "<div class=\"c-donate-tout__inner\">\n  {{#if @close}}\n    <NyprAButton class=\"c-donate-tout__close\" data-test-donate-close @onclick={{@close}} @blank={{true}}>\n      <span class=\"o-icon u-icon--xs o-icon--close\">\n        <NyprASvg @icon='close'/>\n      </span>\n    </NyprAButton>\n  {{/if}}\n\n  <div class=\"c-donate-tout__graphic\">\n    <NyprASvg @icon='pointing-finger'/>\n  </div>\n\n  {{#if (has-block)}}\n    {{yield}}\n  {{else}}\n    <p data-test-donate-message>\n      {{{@message}}}\n    </p>\n  {{/if}}\n\n  <NyprAButton @url={{@linkUrl}}>\n    {{@linkText}}\n  </NyprAButton>\n</div>",
  "nypr-o-donate.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-o-donate';\n\n/**\n  Donate tout\n\n  Inline Usage:\n  ```hbs\n  <NyprODonate @message='hello world' @linkText='click me' @linkUrl='http://example.com/pledge' @close=(action close) />\n  ```\n\n  Block Usage:\n  ```hbs\n  @class nypr-o-donate\n  @yield {Hash} hash\n*/\nexport default Component.extend({\n  layout,\n  classNames: ['c-donate-tout'],\n  classNameBindings: ['isActive'],\n\n  /**\n    Close button action\n\n    @argument close\n    @type {Function}\n  */\n\n  /**\n    Pledge form URL\n\n    @argument linkUrl\n    @type {String}\n  */\n  linkUrl: null,\n\n  /**\n    Text for Donate Link\n\n    @argument linkText\n    @type {String}\n  */\n  linkText: null,\n});",
  "nypr-o-footer.hbs": "<NyprMToTop/>\n\n<footer class=\"c-main-footer\" data-test-main-footer>\n\n  <div class=\"l-container l-wrap u-spacing--triple\">\n\n    <div class=\"c-main-footer__inner\">\n\n      <div class=\"c-main-footer__col1 u-spacing--triple\">\n\n        <div class=\"c-main-footer__branding\">\n\n          <div class=\"c-main-footer__logo\">\n            {{#link-to 'index'}}\n              <NyprASvg @icon='gothamist-logo--stacked'/>\n            {{/link-to}}\n          </div>\n\n          <div class=\"c-main-footer__utility\">\n\n            <a href=\"mailto:tips@gothamist.com\" target=\"_blank\" rel=\"noopener\" class=\"c-main-footer__send-story u-font--secondary-style u-font--xs\"><strong>Send Us a Story Idea</strong></a>\n\n            <div class=\"c-main-footer__social\">\n              <NyprMShareTools @label='Follow Us' as |share|>\n                <share.link @service='facebook' @username='Gothamist'/>\n                <share.link @service='twitter' @username='gothamist' />\n                <share.link @service='instagram' @username='Gothamist' />\n                <share.link @service='youtube' @username='UCY_2VeS5Q9_sMZRhtvF0c5Q'/>\n              </NyprMShareTools>\n            </div>\n          </div>\n\n        </div>\n\n        <div class=\"c-main-footer__slogan u-spacing\">\n          {{yield (hash slogan=(component 'blank-template'))}}\n        </div>\n\n        <div class=\"c-main-footer__nav\">\n          {{yield (hash nav=(component 'nypr-m-secondary-nav'))}}\n        </div>\n\n      </div>\n\n      <div class=\"c-main-footer__col2\">\n\n        <div class=\"c-main-footer__signup\">\n          {{yield (hash signup=(component 'blank-template'))}}\n        </div>\n\n      </div>\n\n    </div>\n\n    <div class=\"c-main-footer__brand-group\">\n      <div class=\"c-main-footer__nypr-logo\">\n        <NyprASvg @icon='nypr-logo'/>\n      </div>\n\n      <NyprMBrandsLinkroll @exclude='gothamist'/>\n    </div>\n\n    <div class=\"c-main-footer__bottom\">\n\n      <div class=\"c-main-footer__copyright\">\n        <p class=\"u-font--secondary-style u-font--xs u-color--white\">&copy; {{moment-format (now) 'YYYY'}} New York Public Radio. All rights reserved.</p>\n      </div>\n\n      <div class=\"c-main-footer__terms-links\">\n        <a href=\"https://www.wnyc.org/terms\" class=\"u-font--secondary-style u-font--xs\" target=\"_blank\" rel=\"noopener\">\n          <strong>Terms of Use</strong>\n        </a>\n        <a href=\"https://www.wnyc.org/privacy\" class=\"u-font--secondary-style u-font--xs\" target=\"_blank\" rel=\"noopener\">\n          <strong>Privacy Policy</strong>\n        </a>\n      </div>\n\n    </div>\n\n  </div>\n\n</footer>",
  "nypr-o-footer.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-o-footer';\n\n/**\n  Site Footer\n\n  @class nypr-o-footer\n  @yield {Hash} hash\n  @yield {Block} hash.slogan `blank-template`\n  @yield {Component} hash.nav `nypr-m-secondary-nav`\n  @yield {Block} hash.signup `blank-template`\n*/\nexport default Component.extend({\n  layout,\n  tagName: '',\n});",
  "nypr-o-gallery-overlay-billboard.hbs": "<div class=\"l-wrap\">\n  <div class=\"o-ad o-ad--billboard\" data-test-gallery-billboard>\n    {{yield}}\n  </div>\n</div>",
  "nypr-o-gallery-overlay-billboard.js": "import Component from '@ember/component';\nimport layout from '../../templates/components/nypr-o-gallery-overlay/billboard';\n\nexport default Component.extend({\n  layout,\n  tagName: '',\n\n  /**\n    Hide the advertising label\n\n    @argument hideLabel\n    @type {Boolean}\n  */\n\n});",
  "nypr-o-gallery-overlay-slide.hbs": "<div class=\"c-slide__header u-spacing--half l-wrap l-container--content\">\n  {{#if @slide.title}}\n    <h2 class=\"c-slide__heading u-font--l\">{{@slide.title}}</h2>\n  {{/if}}\n  <div class=\"c-slide__count u-font--secondary-style u-color--gray--light u-font--xs\">\n    Slide {{this.count}} of {{@total}}\n  </div>\n</div>\n\n<div class=\"c-slide__content u-spacing\">\n\n  <div class=\"c-slide__media u-spacing--half\">\n\n    <div class=\"c-slide__image\">\n      {{!-- TODO: refactor for lazy loaded images --}}\n      <NyprAPicture\n        @width={{@slide.width}}\n        @height={{@slide.height}}\n        @alt={{@slide.alt}}\n        @srcSet={{@slide.srcSet}}\n        @srcS={{@slide.srcS}}\n        @srcM={{@slide.srcM}}\n        @srcL={{@slide.srcL}}\n        @srcXL={{@slide.srcXL}}\n        @breakM={{@breakM}}\n        @breakL={{@breakL}}\n        @breakXL={{@breakXL}}\n      />\n    </div>\n\n    <div class=\"c-slide__meta l-wrap l-container--content\">\n\n      {{#if @slide.credit}}\n        <span class=\"o-credit\">\n          <span class=\"is-vishidden\">image credit </span>\n          {{#if @slide.creditLink}}\n            <a href={{@slide.creditLink}} target=\"_blank\" rel=\"noopener\">\n              {{{@slide.credit}}}\n            </a>\n          {{else}}\n            {{{@slide.credit}}}\n          {{/if}}\n        </span>\n      {{/if}}\n\n      <NyprAToggleBox\n        class=\"c-toggle-box--share\"\n        @calculatePosition={{this.calculatePosition}}\n        @renderInPlace={{true}}\n        as |toggle|>\n        <toggle.trigger @ariaLabel=\"Share this image\">\n          <strong>Share</strong>\n        </toggle.trigger>\n\n        <toggle.dropdown>\n          <NyprMShareTools as |tools|>\n            <tools.share\n              @service='facebook'\n              @url={{this.shareURL}}\n              @ariaLabel=\"Share on Facebook\"\n              @utm={{@share.facebook.utm}}\n            />\n            <tools.share\n              @service='twitter'\n              @url={{this.shareURL}}\n              @ariaLabel=\"Share on Twitter\"\n              @utm={{@share.twitter.utm}}\n              @params={{hash\n                text=@share.twitter.text\n                via=@share.twitter.via\n            }}/>\n            <tools.share\n              @service='reddit'\n              @url={{this.shareURL}}\n              @ariaLabel=\"Share on Reddit\"\n              @utm={{@share.reddit.utm}}\n              @params={{hash\n                title=@share.reddit.title\n            }}/>\n            <tools.share\n              @service='email'\n              @url={{this.shareURL}}\n              @ariaLabel=\"Share via email\"\n              @utm={{@share.email.utm}}\n              @params={{hash\n                subject=@share.email.subject\n                body=@share.email.body\n            }}/>\n          </NyprMShareTools>\n        </toggle.dropdown>\n      </NyprAToggleBox>\n    </div>\n\n  </div>\n\n  <div class=\"c-slide__dek o-rte-text l-wrap l-container--content\">\n    <p>{{{@slide.caption}}}</p>\n  </div>\n\n</div>",
  "nypr-o-gallery-overlay-slide.js": "import Component from '@ember/component';\nimport { computed } from '@ember/object';\nimport { reads } from '@ember/object/computed';\nimport layout from '../../templates/components/nypr-o-gallery-overlay/slide';\n\nexport default Component.extend({\n  layout,\n  classNames: ['c-slide', 'u-spacing--and-half'],\n  attributeBindings: ['data-test-gallery-slide'],\n\n  'data-test-gallery-slide': reads('index'),\n\n  didInsertElement() {\n    this._super(...arguments);\n    this.register(this.element);\n  },\n\n  /**\n    Gallery slide with `title`, `full`, `credit`, and `caption` keys\n\n    @argument slide\n    @type {Object}\n  */\n\n  /**\n    Current one-based index\n\n    @argument index\n    @type {Number}\n  */\n\n  /**\n    Total slide count\n\n    @argument total\n    @type {Number}\n  */\n\n  /**\n    Parameters to pass into share tools. Should have keys for:\n    - facebook\n    - twitter\n    - reddit\n    - email\n\n    @argument share\n    @type {Object}\n  */\n\n  /**\n    Current slide's one-based position\n\n    @field count\n    @type {Number}\n  */\n  count: computed('index', function() {\n    return this.index + 1;\n  }),\n\n  /**\n    Manually pass in the share URL so `image` query param is in sync with\n    the slide itself, and not the current scroll position.\n\n    @accessor shareURL\n    @type {String}\n  */\n  shareURL: computed('index', function() {\n    let { protocol, host, pathname } = window.location;\n    return `${protocol}//${host}${pathname}?image=${this.index}`;\n  }),\n\n  calculatePosition(trigger, content/*, wormhole, options*/) {\n    const BUFFER = 10;\n    let { height, right } = trigger.getBoundingClientRect();\n    let { padding } = window.getComputedStyle(content);\n    padding = parseInt(padding, 10);\n\n    let distanceFromEdge = Math.abs(right - document.documentElement.clientWidth);\n\n    return {\n      style: {\n        right: distanceFromEdge < padding + BUFFER ? -5 : -padding * 2,\n        top: height + 15,\n      }\n    }\n  }\n\n});",
  "nypr-o-gallery-overlay.hbs": "<div class=\"c-gallery-overlay\">\n\n  <div class=\"l-container l-container--xl u-spacing--double\">\n\n    <div class=\"l-wrap l-container--content\">\n      {{#if @parentRoute}}\n        {{#link-to params=@parentRoute class=\"o-back-to-link\" aria-label=(concat \"Related Article: \" @title)}}\n          <span class=\"o-icon o-icon--prev-caret u-path-fill--white u-icon--xs\">\n            <NyprASvg @icon=\"caret-down\"/>\n          </span>\n          <span class=\"u-has-accent u-has-accent--white\" data-test-gallery-title>\n            {{{@title}}}\n          </span>\n        {{/link-to}}\n      {{/if}}\n    </div>\n\n    <div class=\"u-spacing--quad c-slides\">\n\n      {{#each @slides as |slide i|}}\n        {{nypr-o-gallery-overlay/slide\n          register=(action this.registerSlide)\n          slide=slide\n          index=i\n          total=@slides.length\n          data-test-gallery-slide=i\n          share=@share\n          breakM=@breakM\n          breakL=@breakL\n          breakXL=@breakXL\n        }}\n\n        {{yield i (hash billboard=(component 'nypr-o-gallery-overlay/billboard'))}}\n      {{/each}}\n\n      <span class=\"o-end-label c-gallery-end-label u-font--secondary-style u-font--xs u-color--gray\" aria-description=\"End of Gallery\">\n        <span class=\"o-icon o-icon--down-arrow u-icon--m\">\n          <NyprASvg @icon='arrow-dashed'/>\n        </span>\n        End\n      </span>\n\n    </div>\n\n    <div class=\"u-align--center\">\n      {{#if @parentRoute}}\n        {{#link-to params=@parentRoute class=\"o-button c-gallery-overlay__back o-button--c-quaternary\" aria-label=(concat \"Back to Article: \" @title)}}\n          Back to Article\n        {{/link-to}}\n      {{/if}}\n    </div>\n\n  </div>\n</div>",
  "nypr-o-gallery-overlay.js": "import imagesloaded from 'imagesloaded';\n\nimport Component from '@ember/component';\nimport { A } from '@ember/array';\nimport { computed } from '@ember/object';\nimport { debounce, throttle, bind } from '@ember/runloop';\n\nimport layout from '../templates/components/nypr-o-gallery-overlay';\n\nimport { inViewport } from '../helpers/in-viewport';\n\n\n/**\n  Gallery overlay\n\n  @class nypr-o-gallery-overlay\n  @yield {Hash} hash\n  @yield {Component} billboard `nypr-o-gallery-overlay/billboard`\n*/\nexport default Component.extend({\n  layout,\n  classNames: ['c-article__gallery'],\n  classNameBindings: ['takeover::c-article__gallery--static'],\n  attributeBindings: ['data-test-gallery-overlay'],\n  'data-test-gallery-overlay': true,\n\n  init() {\n    this._super(...arguments);\n    this.set('slideEls', A([]));\n  },\n\n  didInsertElement() {\n    // make sure the body height matches the gallery height for scroll measure purposes\n    if (this.takeover) {\n      this._boundResizeListener = bind(this, '_setBodyHeight');\n      window.addEventListener('resize', this._boundResizeListener);\n      this._setBodyHeight();\n    }\n\n    // track which slide is on screen\n    this._boundScrollListener = bind(this, '_currentSlideWatcher');\n    window.addEventListener('scroll', this._boundScrollListener);\n\n    // CSS transition\n    this.element.classList.add('gallery-is-active');\n\n    // scroll to an image if its index is passed in\n    if (this.slideForInit) {\n      let index = this.slideForInit;\n      let slideToScrollTo = this.slideEls[index];\n\n      // wait for all slides to load so scroll position is correct\n      imagesloaded(this.element.querySelectorAll('.c-slide'), () => {\n        // const HEADER_OFFSET = 80;\n\n        let image = slideToScrollTo.querySelector('img');\n        // let { top, height:imageHeight } = image.getBoundingClientRect();\n        //\n        // let imageAtTopOfWindow = top + window.scrollY;\n        // let diff = Math.abs(window.innerHeight - imageHeight);\n        // let targetY = imageAtTopOfWindow - (diff / 2) - HEADER_OFFSET;\n        //\n        // window.scrollTo(0, targetY);\n\n        // TODO: see if this passes QA\n        image.scrollIntoView({block: 'center'});\n      });\n    }\n  },\n\n  willDestroy() {\n    this._super(...arguments);\n    if (typeof FastBoot === 'undefined') {\n      // clear watchers for body height and currently viewed slide\n      window.removeEventListener('scroll', this._boundScrollListener);\n      window.removeEventListener('resize', this._boundResizeListener);\n      document.body.style.height = '';\n    }\n  },\n\n  /**\n    References to slide elements. Set to an empty EmberArray the `init` hook.\n\n    @field slideEls\n    @type {Array[HTMLElement]}\n  */\n  slideEls: null,\n\n  /**\n    Scroll to the slide at the given index on initial render\n\n    @argument slideForInit\n    @type {Number}\n  */\n\n  /**\n    Parameters to pass into each slide's share tools. Should have keys for:\n    - facebook\n    - twitter\n    - reddit\n    - email\n\n    @argument share\n    @type {Object}\n  */\n\n  /**\n    Specify whether the gallery should resize the body to to the gallery's height\n\n    @argument takeover\n    @type {Boolean}\n  */\n  takeover: true,\n\n  /**\n    Parent route for \"return\" links\n    - ['<route name>', '<model or identifier>']\n\n    @argument parentRoute\n    @type {Array[String]}\n  */\n\n  /**\n    Gallery title\n\n    @argument title\n    @type {String}\n  */\n\n  /**\n    Gallery images\n\n    @argument slides\n    @type {Array[Object]}\n  */\n\n  /**\n    Called with current slide object, DOM element, and slide index when\n    a slide enters the viewport\n\n    @argument viewedSlide\n    @type {Function}\n  */\n  viewedSlide() {},\n\n  /**\n    Current slide's index. Updated directly by scroll handler.\n\n    @field currentIndex\n    @type {Number}\n  */\n  currentIndex: 0,\n\n  /**\n    Current slide. Derived from `currentIndex`. If there is no index yet set, it returns the first slide.\n\n    @accessor currentSlide\n    @type {HTMLElement}\n  */\n  currentSlide: computed('slideEls', 'currentIndex', function() {\n    if (!this.slideEls) {\n      return null;\n    }\n\n    return this.slideEls[this.currentIndex];\n  }),\n\n  /**\n    Receives a new slide and adds it to the `slideEls` field\n\n    @method registerSlide\n    @param {HTMLElement} slideEl\n    @return {void}\n  */\n  registerSlide(slideEl) {\n    this.slideEls.pushObject(slideEl);\n  },\n\n  /**\n    Handles some DOM management for scrolling events\n    - calls `viewedSlide` handler when a slide enters the viewport\n    - skips a loop if the gallery has been initialized with a slide\n\n    @method _currentSlideWatcher\n    @param {EventObject} event\n    @return {void}\n  */\n  _currentSlideWatcher(/* e */) {\n    if (this.slideForInit) {\n      this.slideForInit = null;\n      return;\n    }\n    throttle(this, () => {\n      let i;\n      let currentEl;\n      let els = this.slideEls;\n\n      for (i = 0; i < els.length; i++) {\n        let slide = els[i];\n        if (inViewport(slide, {offset: {top: window.innerHeight / 2}})) {\n          currentEl = slide;\n          break;\n        }\n      }\n\n      if (currentEl) {\n        this.viewedSlide(this.slides[i], currentEl, i);\n      }\n    }, 100);\n  },\n\n  /**\n    Force the body's height to match the gallery's height. Enables consistent measurements.\n\n    @method _setBodyHeight\n    @param {EventObject} event\n    @return {void}\n  */\n  _setBodyHeight(/* e */) {\n    debounce(this, () => {\n      let { height } = this.element.getBoundingClientRect();\n      height = `${height}px`;\n      if (document.body.style.height !== height) {\n        document.body.style.height = height;\n      }\n    }, 100);\n  },\n\n});",
  "nypr-o-header-headline.hbs": "{{yield}}",
  "nypr-o-header-headline.js": "import Component from '@ember/component';\nimport layout from '../../templates/components/nypr-o-header/headline';\n\nexport default Component.extend({\n  layout,\n  tagName: 'span',\n  classNames: ['c-main-header__title', 'u-truncate'],\n});",
  "nypr-o-header-leaderboard.hbs": "<div class=\"o-ad o-ad--leaderboard\">\n  {{yield}}\n</div>",
  "nypr-o-header-leaderboard.js": "import Component from '@ember/component';\nimport layout from '../../templates/components/nypr-o-header/leaderboard';\n\nexport default Component.extend({\n  layout,\n  classNames: ['c-main-header__ad-unit'],\n  attributeBindings: ['aria-hidden'],\n\n  'aria-hidden': 'true',\n\n  /**\n    Hide the advertising label\n\n    @argument hideLabel\n    @type {Boolean}\n  */\n\n});",
  "nypr-o-header-left.hbs": "<div class=\"c-menu-toggle\">\n  <NyprAButton class=\"o-menu-toggle\" aria-expanded={{ariaExpanded}} aria-label={{ariaLabel}} @blank={{true}} @onclick={{@openMenu}}>\n    <span class=\"is-vishidden\">Menu</span>\n    <span class=\"o-menu-toggle__line\"></span>\n  </NyprAButton>\n</div>\n\n<div class=\"c-main-header__branding\">\n  {{yield (hash branding=(component 'blank-template'))}}\n</div>\n\n{{yield (hash headline=(component 'nypr-o-header/headline'))}}",
  "nypr-o-header-menu.hbs": "<div class=\"c-menu-toggle\">\n  <NyprAButton class=\"o-menu-toggle\" aria-expanded={{ariaExpanded}} aria-label={{ariaLabel}} @blank={{true}} @onclick={{@close}}>\n    <span class=\"is-vishidden\">Menu</span>\n    <span class=\"o-menu-toggle__line\"></span>\n  </NyprAButton>\n</div>\n\n<div class=\"c-side-menu__group\">\n  <div class=\"c-side-menu__aria-begin is-vishidden\">menu content begins</div>\n\n  <div class=\"c-side-menu__branding\">\n    {{yield (hash branding=(component 'blank-template'))}}\n  </div>\n\n  <NyprAButton class='c-side-menu__donate' @url={{@donateUrl}}>\n    Donate\n  </NyprAButton>\n\n  {{yield (hash primaryNav=(component 'nypr-o-header/nav' class=\"c-primary-nav--side\" linkClass=\"u-has-accent u-has-accent--quaternary\"))}}\n\n  <div class=\"c-side-menu__social\">\n    {{yield (hash social=(component 'nypr-m-share-tools'))}}\n  </div>\n\n</div>\n\n<div class=\"c-side-menu__group\">\n\n  <div class=\"c-side-menu__secondary-nav\">\n    {{yield (hash secondaryNav=(component 'nypr-m-secondary-nav'))}}\n  </div>\n\n  <div class=\"u-spacing\">\n    {{yield (hash\n      search=(component 'nypr-m-inline-search' formClass=\"c-search--side\")\n      tipButton=(component 'nypr-a-button' class=\"c-side-menu__send-story o-button--c-quaternary u-full-width\")\n    )}}\n  </div>\n\n  <div class=\"c-side-menu__nypr-logo\">\n    <NyprASvg @icon='nypr-logo'/>\n  </div>\n\n  <div class=\"c-side-menu__nypr-linkroll\">\n    <NyprMBrandsLinkroll @exclude='gothamist'/>\n  </div>\n\n</div>\n\n<div class=\"c-side-menu__group u-spacing--double\">\n\n  <div class=\"c-side-menu__copyright\">\n    <p class=\"u-font--secondary-style u-font--xs u-color--white\">&copy; {{moment-format (now) 'YYYY'}} New York Public Radio. All rights reserved.</p>\n  </div>\n\n  <div class=\"c-side-menu__terms-links\">\n    <a href=\"https://www.wnyc.org/terms\" class=\"u-font--secondary-style u-font--xs\" target=\"_blank\" rel=\"noopener\">\n      <strong>Terms of Use</strong>\n    </a>\n    <a href=\"https://www.wnyc.org/privacy\" class=\"u-font--secondary-style u-font--xs\" target=\"_blank\" rel=\"noopener\">\n      <strong>Privacy Policy</strong>\n    </a>\n  </div>\n\n  <div class=\"c-side-menu__aria-end is-vishidden\">menu content ends</div>\n</div>\n",
  "nypr-o-header-menu.js": "import ClickOutsideMixin from 'ember-click-outside/mixin';\nimport { closest } from 'ember-click-outside/utils';\n\nimport Component from '@ember/component';\nimport { inject } from '@ember/service';\nimport layout from '../../templates/components/nypr-o-header/menu';\n\nexport default Component.extend(ClickOutsideMixin, {\n  router: inject(),\n\n  layout,\n  classNames: ['c-side-menu'],\n  attributeBindings: ['aria-hidden'],\n\n  /**\n    Items for primary nav\n\n    @argument primaryNav\n    @type {Array<Object>}\n  */\n\n  /**\n    Items for secondary nav\n\n    @argument secondaryNav\n    @type {Array<Object>}\n  */\n\n  /**\n    Function to close menu when user clicks elsewhere in the document\n\n    @argument close\n    @type {Function}\n  **/\n\n  /**\n    Open/closed state\n\n    @argument isOpen\n    @type {Boolean}\n  */\n\n  /**\n    Handler called by `ClickOutsideMixin`\n\n    @method clickOutside\n    @param {Event} e\n    @return {void}\n  */\n  clickOutside(e) {\n    if (!closest(e.target, '.c-menu-toggle') && this.isOpen) {\n      this.close();\n    }\n  },\n\n  didInsertElement() {\n    this._super(...arguments);\n    this.addClickOutsideListener();\n\n    this.router.on('routeDidChange', this.close);\n  },\n\n  willDestroyElement() {\n    this._super(...arguments);\n    this.removeClickOutsideListener();\n  },\n});",
  "nypr-o-header-nav.hbs": "<ul>\n  {{#each @navItems as |item i|}}\n    <li class=\"c-primary-nav__item\">\n      {{#link-to params=item.route class=(concat 'c-primary-nav__link ' @linkClass) data-test-nav-link=i}}\n        {{item.text}}\n      {{/link-to}}\n    </li>\n  {{/each}}\n</ul>",
  "nypr-o-header-nav.js": "import Component from '@ember/component';\nimport layout from '../../templates/components/nypr-o-header/nav';\n\nexport default Component.extend({\n  layout,\n  tagName: 'nav',\n  classNames: ['c-primary-nav'],\n  attributeBindings: ['aria-label'],\n  'aria-label': 'Primary',\n});",
  "nypr-o-header-right.hbs": "{{yield (hash\n  search=(component 'nypr-m-inline-search' formClass=\"c-search--top\")\n)}}",
  "nypr-o-header-right.js": "import Component from '@ember/component';\nimport layout from '../../templates/components/nypr-o-header/right';\n\nexport default Component.extend({\n  layout,\n  classNames: ['c-main-header__right'],\n});",
  "nypr-o-header.js": "import Component from '@ember/component';\nimport { debounce, bind } from '@ember/runloop';\nimport { computed } from '@ember/object';\nimport { htmlSafe } from '@ember/string';\nimport { inject } from '@ember/service';\nimport layout from '../templates/components/nypr-o-header';\n\n\nconst DEBOUNCE_TIMER = 75;\nconst DOWN = 'down';\nconst UP = 'up';\n\n/**\n Site header\n\n See [usage docs](/docs/organisms/nypr-o-header) for more.\n\n @class nypr-o-header\n @yield {Hash} hash\n @yield {Block} hash.leaderboard `nypr-o-header/leaderboard`\n @yield {Boolean} hash.outOfViewport\n @yield {Component} hash.menu `nypr-o-header/menu`\n @yield {Block} hash.menu.branding `blank-template`\n @yield {Component} hash.menu.primaryNav `nypr-o-header/nav`\n @yield {Component} hash.menu.social `nypr-m-share-tools`\n @yield {Component} hash.menu.secondaryNav `nypr-m-secondary-nav`\n @yield {Component} hash.menu.search `nypr-m-inline-search`\n @yield {Component} hash.menu.tipButton `nypr-a-button`\n @yield {Component} hash.left `nypr-o-header/left`\n @yield {Block} hash.left.branding `blank-template`\n @yield {Block} hash.left.headline `nypr-o-header/headline`\n @yield {Component} hash.nav `nypr-o-header/nav`\n @yield {Component} hash.right `nypr-o-header/right`\n @yield {Component} hash.right.search `nypr-m-inline-search`\n @yield {Object} hash.rules Current ruleset as generated by `service:nypr-o-header`\n*/\nexport default Component.extend({\n  layout,\n  tagName: 'header',\n  classNames: ['c-main-header'],\n  classNameBindings: ['isOpen:side-menu-is-active', 'outOfViewport'],\n  attributeBindings: ['spacerHeight:style'],\n\n  headerService: inject('nypr-o-header'),\n\n  /**\n    Controls whether the side menu is open\n\n    @field isOpen\n    @type {Boolean}\n  */\n  isOpen: false,\n\n  /**\n    Indicates whether or not the header has been scrolled up, out of the viewport.\n\n    @field outOfViewport\n    @type {Boolean}\n  */\n  outOfViewport: false,\n\n  /**\n    CSS Selector to target for \"out of viewport\" state\n\n    @argument floatLandmark\n    @type {String}\n  */\n\n  init() {\n    this._super(...arguments);\n\n    this.headerService.register(this);\n  },\n\n  didInsertElement() {\n    this._super(...arguments);\n\n    this._boundListener = bind(this, '_scrollListener');\n    window.addEventListener('scroll', this._boundListener);\n  },\n\n  willDestroyElement() {\n    this._super(...arguments);\n\n    window.removeEventListener('scroll', this._boundListener);\n    // test cleanup\n    document.body.classList.remove('side-menu-is-active');\n  },\n\n  /**\n   Computes current vertical spacing of the header to prevent proceding elements from moving up\n   or down when the header is removed from/added to the document flow.\n\n   Returned as an inline CSS declaration\n\n   @accessor spacerHeight\n   @type {String}\n  */\n  spacerHeight: computed('outOfViewport', function() {\n    let style = '';\n    if (typeof FastBoot === 'undefined' && this.outOfViewport) {\n      let { height } = this.element.getBoundingClientRect();\n      style = `height: ${height}px;`;\n    }\n    return htmlSafe(style);\n  }),\n  /**\n   Toggle open menu state and corresponding state class on body element\n\n   @method toggleMenu\n   @param {Boolean} force optional boolean value to set the value instead of toggling it\n   @param {Event} e click event\n   @return {void}\n  */\n  toggleMenu(force, _e) { // eslint-disable-line no-unused-vars\n    // arg order can change depending where it's called\n    if (force instanceof Event) {\n      _e = force;\n      force = undefined;\n    }\n    if (typeof force === 'boolean') {\n      this.set('isOpen', force);\n    } else {\n      this.toggleProperty('isOpen');\n    }\n\n    document.body.classList.toggle('side-menu-is-active', force);\n\n    if (this.isOpen === true){\n      this.focusMenu();\n    }\n  },\n\n  /**\n    Focus on the side menu\n    For accessibility, so screenreaders go to the menu once it is open\n\n    @method focusMenu\n    @return {void}\n  */\n  focusMenu(){\n    let menu = this.element.querySelector(\".c-side-menu__aria-begin\");\n    menu.setAttribute('tabIndex', -1);\n    menu.focus();\n  },\n\n  /**\n    Measures whether the bottom of the element is out of the viewport\n\n    @method _scrollListener\n    @param {EventObject} event\n    @return {void}\n  */\n  _scrollListener(/* e */) {\n    let direction;\n    let lastScroll = window.pageYOffset;\n    debounce(this, () => {\n      if (lastScroll < window.pageYOffset) {\n        direction = this._lastDirection = DOWN;\n      } else if (lastScroll > window.pageYOffset) {\n        direction = this._lastDirection = UP;\n      } else {\n        direction = this._lastDirection;\n      }\n\n      let el;\n      if (this.floatLandmark) {\n        el = document.querySelector(this.floatLandmark);\n      }\n      if (!el && this.element) {\n        el = this.element;\n      } else if (!el) {\n        return;\n      }\n\n      let { top, height } = el.getBoundingClientRect();\n      let { paddingBottom } = window.getComputedStyle(el);\n\n      let threshold = direction === DOWN ? 0 : height - parseInt(paddingBottom, 10);\n\n      this.set('outOfViewport', top + height < threshold);\n    }, DEBOUNCE_TIMER);\n  }\n});",
  "nypr-o-newsletter-tout.hbs": "<div class=\"c-newsletter-tout__inner\">\n  {{#if @ariaTitle}}<h2 class=\"is-vishidden\">{{@ariaTitle}}</h2>{{/if}}\n  <div class=\"c-newsletter-tout__media\">\n    <NyprAPicture\n      @srcS={{@img.src}}\n      @alt={{@img.alt}}\n    />\n  </div>\n\n  <div class=\"c-newsletter-tout__content u-spacing\">\n\n    <div class=\"c-newsletter-tout__text u-spacing\">\n      <h3 class=\"c-newsletter-tout__heading\" data-test-newsletter-tout-heading>\n        <strong class=\"o-bg-text-accent o-bg-text-accent--senary\">\n          {{yield (hash heading=(component 'blank-template'))}}\n        </strong>\n      </h3>\n      <div class=\"c-newsletter-tout__dek o-rte-text\" data-test-newsletter-tout-blurb>\n        {{yield (hash blurb=(component 'blank-template'))}}\n      </div>\n    </div>\n\n    <NyprMNewsletterForm\n      class=\"c-newsletter-tout__form u-spacing\"\n      @endpoint={{@endpoint}}\n      @params={{@params}}\n      as |form|>\n\n      <form.label/>\n\n      <form.input/>\n\n      <form.submit>\n        <span class=\"c-newsletter-form__submit-icon o-icon o-icon--newsletter-tout\">\n          <NyprASvg @icon='arrow-dashed'/>\n        </span>\n        <span class=\"is-vishidden\">Submit email</span>\n      </form.submit>\n\n      <form.legal>\n        {{yield (hash legal=(component form.legal))}}\n      </form.legal>\n    </NyprMNewsletterForm>\n\n  </div>\n\n</div>",
  "nypr-o-newsletter-tout.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-o-newsletter-tout';\n\nexport default Component.extend({\n  layout,\n  classNames: ['c-newsletter-tout', 'u-reversed-out'],\n});",
  "nypr-o-related-content.hbs": "<h3 class=\"c-related-content__heading u-font--secondary-style u-font--xxs\">Related</h3>\n\n<ul class=\"c-related-content__list u-spacing\">\n  {{#each @items as |item|}}\n    <li>\n      {{#if item.route}}\n        {{#link-to params=item.route class=\"u-has-accent u-has-accent--secondary u-font--s\"}}\n          {{item.title}}\n        {{/link-to}}\n      {{else}}\n        <a\n          href={{item.url}}\n          class=\"u-has-accent u-has-accent--secondary u-font--s\"\n          target=\"_blank\"\n          rel=\"noopener\"\n        >\n          {{item.title}}\n        </a>\n      {{/if}}\n    </li>\n  {{/each}}\n</ul>",
  "nypr-o-related-content.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-o-related-content';\n\n/**\n  List of related content\n\n  Usage:\n  ```hbs\n  <NyprORelatedContent @items={{items}}/>\n  ```\n\n  @class nypr-o-related-content\n*/\nexport default Component.extend({\n  layout,\n  classNames: ['c-related-content', 'u-spacing']\n\n  /**\n    List of items Objects with `title` and `url` keys.\n\n    @argument items\n    @type {Array<Object>}\n  */\n});",
  "nypr-o-sponsored-tout.hbs": "{{#if @heading}}\n  <h2 class=\"c-sponsored-tout__heading\">{{@heading}}</h2>\n{{/if}}\n\n{{yield}}",
  "nypr-o-sponsored-tout.js": "import Component from '@ember/component';\nimport layout from '../templates/components/nypr-o-sponsored-tout';\n\n/**\n  Sponsored content tout\n\n  @class nypr-o-sponsored-tout\n  @yield {Block} block\n*/\nexport default Component.extend({\n  layout,\n  classNames: ['c-sponsored-tout', 'u-breakout', 'o-border-accent'],\n\n  /**\n    Heading text\n\n    @argument heading\n    @type {String}\n  */\n});",
  "picture-example.hbs": "    <NyprAPicture\n      @breakXl=1200\n      @srcXl='https://picsum.photos/1200/800'\n      @breakL=1000\n      @srcL='https://picsum.photos/1000/600'\n      @breakM=800\n      @srcM='https://picsum.photos/800/500'\n      @breakS=600\n      @srcS='https://picsum.photos/600/400'\n      @alt='Pass in an alt tag'\n    />",
  "progress-example.hbs": "    <NyprMProgressBar/>",
  "pullquote-example.hbs": "    <NyprMPullquote\n      @quote=\"It was the best of times, it was the blurst of times.\"\n      @attribution=\"Some Stupid Monkey\"\n    />",
  "related-content-example.hbs": "    <NyprORelatedContent @items={{ITEMS}}/>",
  "sample-gallery.js": "export const GALLERY = [{\n  caption: 'This is a slide caption',\n  credit: 'Getty',\n  src: 'https://picsum.photos/600/300?random',\n  full: 'https://picsum.photos/600/300?random',\n  title: 'This is what a slide title looks like',\n}, {\n  caption: 'This is a slide only a caption; no title or credit',\n  src: 'https://picsum.photos/600/300?random=1',\n  full: 'https://picsum.photos/600/300?random=1',\n}, {\n  title: 'This is a slide with only a title',\n  src: 'https://picsum.photos/600/300?random=2',\n  full: 'https://picsum.photos/600/300?random=2',\n}, {\n  caption: 'This is a slide with no title; just caption & credit',\n  credit: 'Foo Bar/AP News',\n  src: 'https://picsum.photos/600/300?random=3',\n  full: 'https://picsum.photos/600/300?random=3',\n}];",
  "search-block-usage.hbs": "    <NyprMInlineSearch @formClass=\"c-search--top\" as |search|>\n      <search.open/>\n      <search.form @search={{action (mut blockQuery)}}/>\n    </NyprMInlineSearch>",
  "search-usage.hbs": "    <NyprMInlineSearch @search={{action (mut query)}} />",
  "section-heading-example.hbs": "    <NyprASectionHeading>\n      This is a Section Heading\n    </NyprASectionHeading>",
  "share-tools-links.hbs": "    <NyprMShareTools @vertical={{vertical}} as |tools|>\n      <tools.link @service='facebook' @username='foo'/>\n      <tools.link @service='twitter' @username='bar'/>\n      <tools.link @service='instagram' @username='baz'/>\n    </NyprMShareTools>",
  "share-tools-services.js": "export const SERVICE_MAP = {\n  facebook: {\n    profileBase: 'https://www.facebook.com/',\n    shareBase: 'https://www.facebook.com/sharer.php',\n    getParams: url => `u=${url}`,\n  },\n  twitter: {\n    profileBase: 'https://twitter.com/',\n    shareBase: 'https://twitter.com/intent/tweet',\n    getParams: (url, params = {}) => filter({...params, url}),\n  },\n  reddit: {\n    shareBase: 'https://www.reddit.com/submit',\n    getParams: (url, params = {}) => filter({...params, url}),\n  },\n  instagram: {\n    profileBase: 'https://www.instagram.com/',\n    shareBase: '',\n    getParams() {},\n  },\n  youtube: {\n    profileBase: 'https://www.youtube.com/channel/',\n    getParams() {},\n  },\n  email: {\n    shareBase: 'mailto:',\n    profileBase: 'mailto:',\n    getParams: (url, params = {}) => {\n      if (typeof params.subject === 'string') {\n        params.subject = params.subject.replace('{{URL}}', url);\n      }\n      if (typeof params.body === 'string') {\n        params.body = params.body.replace('{{URL}}', url);\n      }\n\n      return filter(params);\n    }\n  },\n};",
  "share-tools-share.hbs": "    <NyprMShareTools as |tools|>\n      <tools.share @service='facebook' />\n      <tools.share @service='twitter' @params={{hash text='Read this great article' via='WNYC'}}/>\n    </NyprMShareTools>",
  "sponsored-tout-usage.hbs": "    <NyprOSponsoredTout @heading=\"Sponsored\">\n      <NyprMBlock @orientation='h' as |block|>\n        <block.media\n          @srcS='/test-image2.jpeg'\n        />\n\n        <block.object as |o|>\n          <o.eyebrow @text='Arts & Entertainment'/>\n\n          <o.title @h3>\n            Sponsored Content\n          </o.title>\n\n          <o.body as |body|>\n            <body.text>\n              Buy this thing because it pays the bills.\n            </body.text>\n\n            <body.meta\n              @timestamp='30 minutes ago'\n              @commentCount=0\n            />\n          </o.body>\n        </block.object>\n      </NyprMBlock>\n    </NyprOSponsoredTout>",
  "story-headline-example.hbs": "    <NyprAStoryHeadline>\n      Headline with Lots of Text Can Sit Here on Multiple Lines\n    </NyprAStoryHeadline>",
  "svg-example.hbs": "    <NyprASvg @icon='wnyc-logo'/>",
  "tag-example.hbs": "    <NyprATag @tag='foo' />",
  "tag-url-example.hbs": "    <NyprATag @tag='foo' @url='https://example.com/foo' />",
  "tag-without-prefix.hbs": "    <NyprATag @prefix='' @tag='foo'/>",
  "tags-block-example.hbs": "    <NyprMTags @tags={{array 'biz' 'baz'}} as |Tag tag|>\n      <Tag @url='/tags/{{tag}}' />\n    </NyprMTags>",
  "tags-example.hbs": "    <NyprMTags @tags={{array 'foo' 'bar'}} />",
  "time.hbs": "    <time datetime={{now}}>{{moment-format (now) 'MMMM Do YYYY, h:mm:ss a'}}</time>",
  "to-top-usage.hbs": "    <NyprMToTop/>",
  "toggle-box-example.hbs": "    <NyprAToggleBox @calculatePosition={{calculatePosition}} as |toggle|>\n      <toggle.trigger>\n        <strong>Toggle Box Label</strong>\n      </toggle.trigger>\n\n      <toggle.dropdown>\n        Dropdown Contents\n      </toggle.dropdown>\n    </NyprAToggleBox>",
  "urgent-style-usage.hbs": "\n    <NyprMBlock @urgent={{true}} as |block|>\n      <block.object as |o|>\n\n        <o.eyebrow @text='Breaking News'/>\n\n        <o.title @h3>\n          <a href=\"\">\n            Families Say NYPD Victim-Blaming Compounds Grief\n          </a>\n        </o.title>\n\n        <o.body as |body|>\n          <body.meta @timestamp='Just Now'/>\n\n          <body.text>\n            <p>A <a href=\"\">coalition of community groups</a> are suing the city to stop a multi-billion dollar mega development.</p>\n          </body.text>\n        </o.body>\n\n      </block.object>\n    </NyprMBlock>\n"
};